.my-gallery {
  // display: none;
  // position: fixed;
  // top: 0;
  // left: 0;
  // width: 100%;
  // height: 100%;
  // z-index: 10000000;
  // animation: var(--animation-fade);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000000;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s;
  pointer-events: none;

  &.active {
    display: flex;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }

  &__overlay {
    background: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100%;
    display: flex;
    padding: 5%;
  }

  &__close {
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    background-color: rgba(0, 0, 0, 0.6);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    transition: background-color 0.3s ease-in-out;

    .material-icons {
      display: inline-block;
      width: 24px;
      height: 24px;
      color: #fff;
      font-size: 24px;
      z-index: 1;
    }

    &:hover {
      background: var(--dark-color);
    }
  }

  &__prev,
  &__next {
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    background-color: rgba(0, 0, 0, 0.6);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: background-color 0.3s ease-in-out;
    z-index: 1;

    .material-icons {
      display: inline-block;
      width: 24px;
      height: 24px;
      color: #fff;
      font-size: 24px;
      z-index: 1;
    }

    &:hover {
      background: var(--dark-color);
    }
  }

  &__prev {
    left: 0;
  }

  &__next {
    right: 0;
  }

  &__body {
    width: 100%;
    max-width: 640px;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  &__item {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    animation: var(--animation-fade);
    position: relative;
    overflow: hidden;
    display: none;

    &.active {
      display: flex;
    }
  }

  &__photo {
    display: flex;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: inline-block;
    }
  }
}
