.footer {
  &__top {
    background: var(--dark-color);
    padding: 16px 0;
  }

  &__top-container {
    &.open {
      align-items: flex-start;
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }

  &__nav {
    position: relative;
    display: flex;
    gap: 12px;
  }

  &__toggle {
    cursor: pointer;
    width: 24px;
    height: 24px;
    background-image: url("data:image/svg+xml;base64,");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transition: all 0.2s ease-in-out;
    display: none;
    animation: var(--animation-fade);

    &.active {
      background-image: url("data:image/svg+xml;base64,");
      transform: rotate(180deg);
    }

    @media (max-width: 800px) {
      display: inline-block;
    }
  }

  &__menu {
    list-style-type: none;
    display: flex;
    gap: 24px;

    li > a {
      cursor: pointer;
      text-transform: uppercase;
      color: rgba(#fff, 0.5);
      font-weight: 600;
      text-decoration: none;
      transition: color 0.3s ease-in-out;
      &:hover {
        color: var(--main-color);
      }
    }
  }

  &__menu-item {
    animation: var(--animation-fade);
  }

  &__social {
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 12px;
  }

  &__social-link {
    cursor: pointer;
    color: rgba(#fff, 0.4);
    transition: color 0.3s ease-in-out;
    &:hover {
      color: var(--main-color);
    }

    svg {
      width: 20px;
      height: 20px;
    }
  }

  &__bottom {
    background: #fff;
    padding: 12px 0;
  }

  &__img {
    img {
      display: inline-block;
    }
  }

  &__copyright {
    color: var(--text-gray);
    font-size: 14px;
  }
}

@media (max-width: 1024px) {
  .footer__logo {
    display: none;
  }
}

@media (max-width: 800px) {
  .footer__top {
    padding: 12px 0;
  }

  .footer__menu {
    flex-direction: column;
    gap: 8px;

    &-item {
      display: none;
      &.item-help {
        display: block;
      }
    }

    li > a {
      font-size: 14px;
      font-weight: 700;
    }
  }

  .footer__menu-item.show {
    display: block;
  }
}

@media (max-width: 480px) {
  .footer__bottom .footer__container {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 0;
  }
}
