.private-blur {
  width: 100%;
  height: 100%;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(8px);
  border-radius: 8px;
  padding: 8px;
  position: absolute;
  left: 0;
  top: 0;

  &__icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    font-size: 24px;
    color: rgba(#fff, 0.5);
    margin-bottom: 8px;
  }

  &__text {
    color: rgba(#fff, 0.9);
    font-weight: 700;
    line-height: 1;
  }
}
