.gift-item {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
  border: 1.5px solid transparent;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
  text-align: center;

  &:hover {
    border: 1.5px solid var(--main-color);
    transform: scale(1.02);

    @media (max-width: 1024px) {
      transform: none;
    }
  }

  &__header {
    padding: 8px;
    aspect-ratio: 1 / 1;
  }

  &__img {
    width: 100%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
    text-align: center;
    padding: 24px;
    padding-top: 0;
    padding-bottom: 16px;
    position: relative;
    margin-top: -16px;
  }

  &__price {
    width: 96px;
    height: 48px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-image: url("data:image/svg+xml;base64,");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 100px;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    padding: 4px;
  }

  &__name {
    display: inline-block;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 700;
  }
}

@media (max-width: 390px) {
  .gift-item__name {
    font-size: 16px;
  }

  .gift-item__price {
    font-size: 16px;
  }
}
