.page-newsfeed {
  position: relative;

  .page-title {
    margin: 0;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__nav {
    display: flex;
    &-item {
      @extend .albums-nav__item;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;

    .news-feed__item {
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }

    .news-feed__media {
      height: 380px;
    }
  }
}

@media (max-width: 1024px) {
  .page-newsfeed__grid {
    gap: 24px;
  }
  .page-newsfeed__grid .news-feed__media {
    height: 320px;
  }
}

@media (max-width: 800px) {
  .page-newsfeed__grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  .page-newsfeed__grid .news-feed__media {
    height: 480px;
  }
}

@media (max-width: 640px) {
  .page-newsfeed__grid .news-feed__media {
    height: 320px;
  }

  .page-newsfeed__header {
    margin-bottom: 24px;
  }
}
