.video-item {
  position: relative;
  border-radius: 12px;
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;

  .upload-file {
    width: 100%;
    height: 100%;
  }

  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &::poster {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-width: 0;
    outline-width: 0;
  }
}
