@import "./01-normalize";
@import "./_02-fonts";
@import "./03-vars";

/* ------------------- Default -------------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  &::before,
  &::after {
    box-sizing: border-box;
  }
}

body {
  min-width: 320px;
  background: var(--body-bg);
  color: var(--text-color);
  font-family: var(--font-default);
  font-size: var(--fz-body);
  line-height: var(--lh-body);
  color: var(--text-color);
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page__body {
  &.lock-scroll {
    overflow: hidden;
  }
}

//  footer  
.page__wrapper {
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.page__main {
  flex: 1;
}

.footer {
  margin-top: auto;
}

.hidden {
  display: none !important;
}

.container {
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 16px;

  @media (max-width: 1024px) {
    padding: 0 24px;
  }

  @media (max-width: 599px) {
    padding: 0 16px;
  }
}

.page-padding {
  padding-top: clamp(1.5rem, 1.318rem + 0.91vw, 2rem);
  padding-bottom: clamp(3rem, 2.636rem + 1.82vw, 4rem);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--title-color);
}

.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5,
.title-h6 {
  color: var(--title-color);
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  cursor: pointer;
  font-size: inherit;
  color: inherit;
  transition: all 0.2s ease-in-out;

  &:hover {
    text-decoration: none;
  }
}

input,
button,
textarea,
select {
  font-family: var(--font-default);
}

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  background-color: none;
  font-size: inherit;
}

input,
select {
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  outline: none;
  border-radius: 0;
  padding: 0;
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  outline: none;
  color: inherit;

  &::placeholder {
    color: #666;
  }
}

select::-ms-expand {
  display: none;
}

strong,
.strong {
  font-weight: bold;
  font-weight: 700;
}

.list-reset {
  list-style: none;
}

.text-center {
  text-align: center;
}

.text-number {
  color: var(--text-gray);
  font-weight: 400;
}

.hidden {
  display: none;
}

//     
// animation: fade 0.3s ease-in-out (     ;
@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.text-date {
  font-weight: 400;
  color: var(--text-gray);
}

.new-counter {
  font-weight: 700 !important;
  color: var(--pink-color) !important;
}

.flex {
  display: flex;

  &.justify-sb {
    justify-content: space-between;
  }

  &.justify-center {
    justify-content: center;
  }

  &.align-center {
    align-items: center;
  }
}

.material-icons {
  transition: color 0.3s ease-in-out;
}
