.verif-msg {
  width: 100%;
  background: var(--yellow-light-color);
  padding: 8px 16px;
  animation: var(--animation-fade);
  position: relative;
  z-index: 10;

  &.mob-verif {
    display: none !important;
    &.hide {
      display: none !important;
    }

    &.show {
      display: block !important;
    }
  }

  &.hide {
    display: none;
  }

  &.show {
    display: block;
  }

  &__step {
    display: none;
    animation: var(--animation-fade);
    &.active {
      display: block;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    font-size: 15px;
    font-weight: 500;
  }

  &.show {
    display: block;
  }

  &.hide {
    display: none !important;
  }

  &__link {
    cursor: pointer;
    font-weight: 600;
    color: var(--system-color);

    text-decoration: underline;
  }

  &__icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    font-size: 24px;
    color: var(--system-color);
  }

  .verif-step2,
  .verif-step3 {
    .verif-msg__content {
      align-items: flex-start;
      gap: 12px;
    }

    .verif-msg__icon {
      width: 24px;
      height: 24px;
      font-size: 24px;
    }
  }

  .verif-step3 {
    .verif-msg__icon {
      width: 30px;
      height: 30px;
      font-size: 30px;
    }

    .box {
      display: flex;
      align-items: center;
      gap: 8px;
    }

    .verif-msg__content {
      justify-content: space-between;
      align-items: center;
      gap: 16px;
    }
  }

  &__form {
    flex-shrink: 0;
    width: 290px;
    height: 40px;
    position: relative;
  }

  &__form-input {
    width: 100%;
    height: 100%;
    font-size: 16px;
    line-height: 1;
    border: 1px solid var(--system-color);
    color: var(--text-color);
    background-color: #fff;
    border-radius: 4px;
    padding-left: 12px;
    padding-right: 40px;
    transition: all 0.2s ease-in-out;
    &:focus {
      background-color: #fff;
      border-color: var(--system-color);
      &::placeholder {
        color: var(--text-color);
        font-weight: 400;
      }
    }
    &::placeholder {
      color: var(--system-color);
      font-weight: 500;
    }
  }
  &__form-btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    color: var(--system-color);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
  }
}

.verif-msg {
  padding: 10px;

  .verif-msg__content {
    align-items: flex-start;
  }
}

@media (max-width: 1024px) {
  .verif-msg {
    display: none !important;

    &.mob-verif {
      display: block !important;
      padding: 16px;

      .verif-step3 .verif-msg__content {
        flex-direction: column;
        gap: 8px;
      }

      .verif-msg__form {
        width: 100%;
      }
    }
  }

  .verif-msg__text {
    font-size: 14px;
    font-weight: 600;
  }

  .verif-msg .verif-step3 .verif-msg__content {
    // flex-direction: column;
    gap: 16px;
  }

  .verif-msg .verif-step3 .box {
    align-items: flex-start;
  }
}
