.btn-scroll {
  background: var(--blue-color);
  box-shadow: var(--bx-blue);
  position: fixed;
  top: 20%;
  right: -100%;
  z-index: 20;
  opacity: 0;
  transition: all 0.4s ease-in-out;

  &:hover {
    background: var(--blue-color);
    box-shadow: none;
  }

  &__icon {
    color: #fff;
    fill: #fff;
  }

  &.show {
    opacity: 1;
    right: 16px;
  }
}

@media (max-width: 599px) {
  .btn-scroll {
    width: 48px !important;
    height: 48px !important;
  }
}
