.modal-photos {
  .modal__content {
    max-width: 800px;
    padding: 24px;

    @extend .scrollbar-custom;
  }

  .modal__header {
    margin-bottom: 16px;
  }

  .modal__title {
    color: var(--main-color);
    font-size: 32px;
    margin-bottom: 8px;
    span {
      color: var(--title-color);
      font-size: 24px;
      position: relative;
      &::before {
        content: "/";
        display: inline-block;
        margin-right: 8px;
        font-weight: 400;
        color: var(--line-dark);
      }
    }
  }

  .modal__descr {
    font-size: 18px;
    opacity: 0.7;
  }

  .page-tabs__item::before {
    background: var(--main-color);
  }

  .page-tabs__item.active {
    color: var(--text-color);
  }

  .page-tabs__item:hover {
    color: var(--main-color);
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
    margin-top: 24px;
  }

  .go-photomanager {
    &:hover {
      .material-icons {
        color: #fff;
      }
    }
  }
}

.modal-photos__nav {
  .pagination {
    padding-bottom: 0;
    justify-content: center;
  }
}

// Grid Photos
.modal-photos__grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 16px;
  padding-top: 24px;
}

.modal-photos .upload-photo {
  width: 100%;
  height: 100%;
}

.modal-photos__photo {
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  overflow: hidden;
  position: relative;

  &.photo-empty {
    height: 100% !important;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(#000, 0.4);
    border-radius: 12px;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &::after {
    content: "";
    display: inline-block;
    width: 40px;
    height: 40px;
    background: url("data:image/svg+xml;base64,")
      no-repeat center;
    background-size: cover;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &:hover {
    &::before,
    &::after {
      opacity: 1;
    }
  }
}

// Albums Nav
.albums-nav {
  word-wrap: nowrap;
  overflow-x: auto;
  display: flex;

  &__item {
    flex-shrink: 0;
    text-decoration: none;
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    font-size: 14px;
    font-weight: 700;
    line-height: 1;
    padding: 0 24px;
    border: 1px solid var(--line-dark);
    border-radius: 100px;
    text-align: center;
    transition: all 0.3s ease-in-out;
    &:not(:last-child) {
      margin-right: 12px;
    }
    &:hover {
      border-color: var(--main-color);
    }

    &.active {
      color: #fff;
      font-weight: 800;
      border: 1px solid transparent;
      background: var(--main-color);
    }
  }
}

@media (max-width: 800px) {
  .modal-photos__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 600px) {
  .modal-photos__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 480px) {
  .modal-photos {
    .modal__content {
      margin: 0 auto;
      max-height: 87%;
    }
  }

  .modal-photos__grid {
    gap: 8px;
    // max-height: 440px;
    // overflow-y: scroll;
  }

  .modal-photos__photo,
  .modal-photos__photo::before,
  .modal-photos .upload-file__label {
    border-radius: 8px;
  }

  .modal-photos .upload-file__icon {
    width: 40px;
    height: 40px;
  }

  .modal-photos .upload-file__text {
    font-size: 12px;
  }
}

@media (max-width: 390px) {
  .modal-photos .modal__content {
    padding: 20px;
  }

  .modal-photos .modal__title {
    font-size: 24px;

    span {
      font-size: 18px;
    }
  }

  .modal-photos .modal__descr {
    font-size: 16px;
  }
}

@media (max-width: 375px) {
  .modal-photos__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
