$letter-photo-width: 96px;
$letter-photo-height: 144px;

.letter-item {
  border-radius: 12px;
  padding: 24px;
  overflow: hidden;

  &:not(:last-child) {
    margin-bottom: 24px;
  }

  &:nth-child(odd) {
    margin-right: 5%;
  }

  &:nth-child(even) {
    margin-left: 5%;
  }

  &__profile {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 18px;
    font-weight: 700;
  }

  &.letter-man {
    background: #eff7fc;
    border: 1px solid var(--blue-color);

    .letter-textsize__lg {
      color: var(--blue-color);
    }
    .letter-textsize__sm:hover {
      color: var(--blue-color);
    }

    .letter-item__profile {
      color: var(--blue-color);
    }
  }

  &.letter-woman {
    background: #fff;
    border: 1px solid var(--line-dark);

    .letter-from__link {
      color: var(--pink-color);
    }

    .letter-textsize__lg {
      color: var(--pink-color);
    }

    .letter-textsize__sm:hover {
      color: var(--pink-color);
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-bottom: 16px;

    &-left {
      display: flex;
      align-items: center;
      gap: 16px;
    }

    &-right {
      display: flex;
      align-items: center;
      gap: 16px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    gap: 16px;
    color: var(--text-gray);

    .letter-item__date,
    .letter-item__time {
      display: inline-flex;
      align-items: center;
      gap: 8px;
      .material-icons {
        display: inline-block;
        width: 20px;
        height: 20px;
        font-size: 20px;
        color: var(--icon-gray);
      }
    }
  }

  &__body {
    position: relative;
  }

  &__text {
    font-size: 16px;
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  &__photo {
    cursor: pointer;
    text-decoration: none;
    display: block;
    margin-left: 16px;
    margin-bottom: 16px;
    float: right;
    text-decoration: none;
    display: block;
    width: $letter-photo-width;
    height: $letter-photo-height;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    z-index: 0;
    transition: all 0.3s ease-in-out;
    &:hover {
      transform: scale(1.02);
      opacity: 0.8;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
    }

    &-num {
      width: max-content;
      height: 24px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      background: rgba(#000, 0.8);
      border-radius: 100px;
      font-size: 14px;
      font-weight: 700;
      padding: 0 8px;
      position: absolute;
      bottom: 8px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

.photo-gallery {
  .letter-item__photo {
    display: block;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &.is-hide {
      display: none;
    }

    &:hover {
      transform: scale(1.02);
      opacity: 0.6;
    }
  }
}

.letter-date {
  color: var(--text-gray);
}

.letter-textsize {
  display: flex;
  align-items: baseline;
  gap: 12px;

  &__lg {
    cursor: pointer;
    display: inline-block;
    color: var(--blue-color);
    font-size: 20px;
    font-weight: 700;
    line-height: 1;
    transition: opacity 0.3s ease-in-out;
    &:hover {
      opacity: 0.8;
    }
  }

  &__sm {
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    line-height: 1;
    transition: color 0.3s ease-in-out;
    &:hover {
      color: var(--blue-color);
    }
  }
}

@media (max-width: 600px) {
  .letter-item__body {
    grid-template-columns: 1fr;
  }

  .letter-item__photo {
    width: 96px;
    order: -1;
  }
}

@media (max-width: 599px) {
  .letter-item {
    padding: 16px;
  }

  .letter-item__header-left {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }

  .letter-item__info {
    font-size: 14px;
    .material-icons {
      width: 18px !important;
      height: 18px !important;
      font-size: 18px !important;
    }
  }
}

@media (max-width: 480px) {
  .letter-item {
    padding: 16px;
    border-radius: 8px;

    .letter-textsize {
      gap: 16px;
    }
  }

  .letter-item__text {
    font-size: 15px;
  }

  .letter-item:not(:last-child) {
    margin-bottom: 16px;
  }
}
