.burger {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background: transparent;
  border-radius: 50%;
  position: relative;

  &__icon {
    display: inline-block;
    width: 22px;
    height: 2.5px;
    background-color: #fff;
    border-radius: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: background-color 0.2s ease-in-out;

    &::before,
    &::after {
      content: "";
      width: 100%;
      height: 2.5px;
      background-color: #fff;
      border-radius: 100px;
      position: absolute;
      left: 0;
      opacity: 1;
      transition: transform 0.2s ease-in-out;
    }

    &::before {
      top: -8px;
    }

    &::after {
      bottom: -8px;
    }
  }

  &.active {
    width: 48px;
    height: 48px;
    background: transparent !important;
    backdrop-filter: none !important;
    top: 4px !important;
    right: 4px !important;
    z-index: 10000;
    .burger__icon {
      background: transparent;

      &::before {
        background-color: rgba(#000, 0.4);
        top: 20%;
        transform: rotate(45deg);
      }

      &::after {
        background-color: rgba(#000, 0.4);
        top: 0;
        bottom: 50%;
        transform: rotate(-45deg);
      }
    }
  }
}
