.swiper-inner {
  width: 100%;
  position: relative;
}

.swiper-container {
  max-width: var(--container-width);
  height: auto;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.swiper-wrapper {
  box-sizing: content-box;
  max-width: var(--container-width);
  height: auto;
  display: flex;
  // justify-content: center;
}

.swiper-slide {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.swiper-prev,
.swiper-next {
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid transparent;
  box-shadow: var(--bx-popup);
  transition: all 0.3s ease-in-out;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  display: inline-flex !important;

  .material-icons {
    color: var(--main-color);
  }

  &:hover {
    color: #fff;
    background: var(--main-color);
    border: 2px solid #fff;
    box-shadow: var(--bx-pink);

    .material-icons {
      color: #fff;
    }
  }
}

.swiper-prev {
  left: -24px;
  .material-icons {
    width: 18px;
    height: 18px;
    font-size: 18px;
    position: relative;
    left: 4px;
  }
}

.swiper-next {
  right: -24px;
}

.swiper-pagination {
  position: absolute !important;
  top: inherit !important;
  right: inherit !important;
  bottom: -32px !important;
  left: 50% !important;
  transform: translateX(-50%) !important;
  z-index: 1 !important;
}

.swiper-pagination-bullet {
  width: 8px !important;
  height: 8px !important;
  opacity: 0.15 !important;
}

.swiper-pagination-bullet-active {
  background: var(--main-color) !important;
  opacity: 1 !important;
}

.slider-profiles-two {
  .swiper-prev,
  .swiper-next {
    display: none !important;
  }

  .swiper-pagination {
    bottom: -24px !important;
  }
}

.slider-profiles-more {
  .swiper-pagination {
    display: none;
    @media (max-width: 1024px) {
      display: block !important;
    }
  }

  .swiper-prev,
  .swiper-next {
    @media (max-width: 1024px) {
      display: none !important;
    }
  }
}

.profile-slider-inner {
  position: relative;
  overflow: hidden;

  .swiper-pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    height: 56px !important;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.6) 100%
    );
    bottom: 0 !important;
    z-index: 10;
  }

  .swiper-pagination-bullet {
    background: #fff !important;
    opacity: 0.3 !important;
  }

  .swiper-pagination-bullet-active {
    width: 12px !important;
    height: 12px !important;
    background: var(--main-color) !important;
    border: 2px solid #fff;
    opacity: 1 !important;
  }

  .profile-slider {
    display: flex;
    height: 100%;
  }

  .swiper-slide {
    height: 100%;
  }
}
