.anim-pulse {
  animation: pulseAnim 2s linear infinite;
}

.anim-rotate {
  animation: rotateAnim 1.5s linear infinite;
}

@keyframes pulseAnim {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes rotateAnim {
  0% {
    transform: rotate(-15deg);
  }

  50% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-15deg);
  }
}
