.form {
  width: 100%;
  max-width: 480px;
  background-color: #fff;
  border-radius: 24px;
  padding: clamp(1rem, 0.5rem + 5vw, 2rem) clamp(1.5rem, -1.5rem + 15vw, 3rem);

  @media (max-width: 480px) {
    border-radius: 16px;
  }

  &__header {
    margin-bottom: clamp(1.25rem, -0.25rem + 7.5vw, 2rem);

    &-title {
      color: var(--title-color);
      font-size: clamp(1.5rem, 0.5rem + 5vw, 2rem);
      font-weight: 600;
      line-height: 1.2;
      margin-bottom: 8px;
    }

    &-descr {
      color: var(--text-color);
      opacity: 0.8;

      @media (max-width: 360px) {
        font-size: 14px;
      }
    }
  }

  &__box {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__btn {
    width: 100%;
  }

  &__footer {
    margin-top: 16px;
  }

  &__group {
    .input-field {
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }

    display: grid;
    gap: 16px;

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &.grid-2 {
      grid-template-columns: repeat(2, 1fr);
    }

    &.grid-3 {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

.error-message {
  color: red;
  font-size: 14px;
  line-height: 1;
  display: none;
  margin-top: 4px;
  animation: fade 0.3s ease-in-out;

  &.active {
    display: block;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media (max-width: 600px) {
  .form__group.grid-2,
  .form__group.grid-3 {
    grid-template-columns: 1fr;
  }
}
