.chat-form {
  width: 100%;

  &__field {
    position: relative;
  }

  &__input {
    height: 56px !important;
    border: none;
    padding-right: 96px;
    &:focus {
      border: none;
    }
  }

  &__btn {
    width: 44px;
    height: 44px;
    position: absolute;
    top: 4px;
    right: 4px;
    color: #fff;
    &-icon {
      display: inline-block;
      width: 20px;
      height: 20px;
      font-size: 20px;
      color: #fff;
      position: relative;
      left: 2px;
    }
  }

  .btn-chatsmile {
    position: absolute;
    top: 50%;
    right: 64px;
    transform: translateY(-50%);
  }
}
