/* ------------------- Modal -------------------- */

//  
$modal-overlay: var(--overlay-modal);

//   .modal__content
$modal-content-width: 640px;
$modal-form-width: 480px;
$modal-bg: #fff;
$modal-radius: 16px;
$modal-pading: 32px;

//   
.modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000000;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s;
  pointer-events: none;

  //   
  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events: all;

    .modal__content {
      transform: translateY(0);
    }
  }

  &__overlay {
    width: 100%;
    height: 100%;
    background-color: $modal-overlay;
    display: flex;
    padding: 20px;
    @media (max-width: 480px) {
      padding: 16px;
      padding-top: 24px;
    }
  }

  &__content {
    width: 100%;
    max-height: 90%;
    max-width: $modal-content-width;
    margin: auto;
    overflow: auto;
    background: $modal-bg;
    border-radius: $modal-radius;
    transform: translateY(-100px);
    padding: $modal-pading;
    transition: transform 0.3s ease-in-out;
    position: relative;

    @media (max-width: 480px) {
      max-height: 95%;
      padding: 24px 20px;
    }
  }

  &__close {
    cursor: pointer;
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url("data:image/svg+xml;base64,");
    background-size: cover;
    position: absolute;
    top: 16px;
    right: 16px;
    opacity: 0.3;
    transition: all 0.3s ease-in-out;

    &:hover {
      background: url("data:image/svg+xml;base64,");
      opacity: 1;
    }
  }

  &__header {
    margin-bottom: 32px;

    @media (max-width: 480px) {
      margin-bottom: 20px;
    }
  }

  &__profile-photo {
    @extend .modal-profile-contacts__photo;
  }

  &__icon {
    flex-shrink: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 112px;
    height: 112px;
    border: 1px solid var(--line-dark);
    border-radius: 50%;
    margin-bottom: 16px;

    @media (max-width: 480px) {
      width: 96px;
      height: 96px;
    }

    img {
      width: 48px;
      height: 48px;
    }

    .material-icons {
      width: 48px;
      height: 48px;
      font-size: 48px;
    }

    @media (max-width: 375px) {
      width: 104px;
      height: 104px;
      img {
        width: 40px;
        height: 40px;
      }

      .material-icons {
        width: 40px;
        height: 40px;
        font-size: 40px;
      }
    }
  }

  &__title {
    color: var(--title-color);
    font-size: 28px;
    font-weight: 700;
    line-height: 1.2;
    margin-bottom: 12px;

    @media (max-width: 480px) {
      font-size: 24px;
      margin-bottom: 8px;
    }
  }

  &__descr {
    color: var(--text-color);
    font-size: 17px;
    opacity: 0.8;
  }

  &__btns {
    @media (max-width: 480px) {
      flex-direction: column;
      gap: 12px;
    }
  }

  &.modal-form {
    .modal__content {
      max-width: $modal-form-width;
    }

    .form {
      background: none;
      padding: 0;
      border-radius: 0;
      box-shadow: none;
    }
  }

  &__btn {
    width: 100%;
  }

  &__btns {
    display: flex;
    gap: 12px;
    .btn {
      width: 100%;
    }
  }
}
