/* ------------------- Custom Select -------------------- */
.select {
  display: flex;
  flex-direction: column;
  gap: 6px;
  position: relative;
  width: 100%;

  //  
  &__label {
    display: block;
    color: var(--text-gray);
    box-sizing: border-box;
    pointer-events: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: all 0.2s ease 0s;
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  &__trigger {
    display: flex;
    align-items: center;
    width: 100%;
    height: 56px;
    color: var(--text-color);
    background-color: #fff;
    border: 1px solid var(--line-dark);
    border-radius: 8px;
    padding: 0 20px;
    padding-right: 15px;
    transition: border-color 0.2s ease-in-out, box-shadow 0.15s ease-in-out;
    cursor: pointer;
    position: relative;

    &:hover {
      border-color: var(--blue-color);
    }

    // 
    &::after {
      content: "";
      position: absolute;
      top: 53%;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      right: 15px;
      width: 18px;
      height: 18px;
      background: url(data:image/svg+xml;base64,);
      background-size: contain;
      background-repeat: no-repeat;
      transition: all 0.4s ease-in-out;
      opacity: 0.8;
    }

    //  
    &.disabled {
      background-color: #f2f4f7;
    }
  }

  //  
  &__selected {
    color: var(--text-color);
    font-size: 16px;
    font-weight: 400;
  }

  //  
  &__options {
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    width: 100%;
    max-height: 0;
    overflow-y: auto;
    list-style: none;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: var(--bx-dropdown);
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    animation: fade 0.3s ease-in-out;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    z-index: 10;

    &::-webkit-scrollbar {
      width: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--blue-color);
      border-radius: 6px;
    }
  }

  // 
  &__option {
    cursor: pointer;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
    padding: 12px 15px;
    transition: background-color 0.25s ease-in-out;
    position: relative;

    &:hover {
      background-color: var(--blue-light-color);
    }

    &.selected {
      background-color: var(--blue-color);
      color: #fff;
      font-weight: 500;
      position: relative;

      &::after {
        content: "";
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("data:image/svg+xml;base64,")
          no-repeat center;
        background-size: cover;
        position: absolute;
        top: 50%;
        right: 5px;
        transform: translate(-50%, -50%);
      }
    }
  }

  //  
  &.open {
    position: relative;

    .select__label {
      cursor: default;
      top: 0;
      left: 15px;
      transition: top 0.2s;
      background: #fff;
      color: var(--text-gray);
      font-weight: 400;
      font-size: 14px;
      padding: 0 5px;
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }

    .select__trigger {
      border-color: var(--blue-color);

      &::after {
        transform: rotateX(180deg) translateY(50%);
      }
    }

    .select__options {
      max-height: 200px;
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
  }

  &.select-sm {
    .select__trigger {
      height: 48px;
      padding: 0 16px;
    }

    .select__label {
      font-size: 15px;
    }
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media (max-width: 480px) {
  .select {
    &__trigger {
      height: 48px;
      padding: 0 16px;
    }
  }
}
