.photo-empty {
  width: 100%;
  height: 100%;
  border-radius: 16px;
  border: 1px dashed #999;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
  z-index: -1;

  &.hide {
    display: none;
  }

  &__center {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }

  &__icon {
    flex-shrink: 0;
    display: block;
    width: 128px;
    height: 128px;
  }

  &__center {
    color: #bdbdbd;
    font-size: 18px;
    font-weight: 400;
  }

  &.sm {
    border-radius: 8px;
    border-width: 1px;

    .photo-empty__center {
      gap: 8px;
    }
    .photo-empty__label {
      font-size: 14px;
      font-weight: 600;
    }

    .photo-empty__icon {
      width: 40px;
      height: 40px;
    }
  }
}
