.letter-options {
  display: flex;
  align-items: center;
  gap: 12px;

  &__check {
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    gap: 0 !important;
    overflow: hidden;
  }

  &__delete {
    cursor: pointer;
    color: var(--icon-gray);
    &:hover {
      color: var(--system-color);
    }
  }
}
