$post-height: 520px;
$vertical-width: 338px;

.news-feed {
  display: flex;
  flex-direction: column;
  // gap: 16px;

  .br-none {
    border-radius: none;
  }

  &__item {
    width: 100%;
    border: 1px solid var(--line-dark);
    border-radius: 16px;
    padding: 16px;
    overflow: hidden;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__item-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__profile {
    display: flex;
    align-items: center;

    &-ava {
      margin-right: 12px;
    }
  }

  &__profile-body {
    display: flex;
    flex-direction: column;
    gap: 0;
  }

  &__profile-name {
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
  }

  &__date {
    color: var(--text-gray);
  }

  &__item-text {
    font-size: 18px;
    margin-bottom: 24px;
  }

  &__media {
    border-radius: 12px;
    height: $post-height;
    // aspect-ratio: 4 / 3;

    .media-item {
      width: 100%;
      height: 100%;
      display: inline-block;
      border-radius: 12px;
      overflow: hidden;
      transition: opacity 0.3s ease-in-out;

      &:hover {
        opacity: 0.8;
      }

      .news-feed__item-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &.items-two {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 16px;
      aspect-ratio: inherit;

      &::before {
        display: none;
      }
    }

    &.items-three {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 16px;
      aspect-ratio: inherit;

      &::before {
        display: none;
      }
    }

    &.vertical-center {
      text-align: center;
      width: 100%;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      position: relative;
      overflow: hidden;
      z-index: 1;

      &::before {
        content: "";
        width: 100%;
        height: 100%;
        background: rgba(#fff, 0.2);
        backdrop-filter: blur(16px);
        border-radius: 12px;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
      }

      .media-item {
        width: 50%;
        border-radius: 0;
      }
    }
  }

  &__more {
    width: 100%;
    border-color: var(--line-dark);

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
}

.news-feed__media .video-item {
  background: var(--dark-color);
}

.news-feed__media-video {
  height: auto !important;
}

@media (max-width: 1024px) {
  .news-feed__media {
    height: 420px;
  }
}

@media (max-width: 600px) {
  .news-feed__media {
    height: 340px;
  }
}

@media (max-width: 480px) {
  .news-feed__media {
    height: 280px;
  }

  .news-feed__media.items-two {
    gap: 8px;
  }

  .news-feed__item-text {
    font-size: 16px;
    margin-bottom: 16px;
  }
}

@media (max-width: 390px) {
  .news-feed__media {
    height: 200px;
  }
}
