.mob-nav {
  width: 100%;
  height: 64px;
  // background: var(--dark-color);
  background: rgba(#212121, 0.9);
  backdrop-filter: blur(10px);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  transition: bottom 0.3s ease-in-out;
  display: none;

  @media (max-width: 992px) {
    display: block;
  }

  &.hide {
    bottom: -100%;
  }

  &__list {
    height: 100%;
    list-style: none;
    display: flex;
    justify-content: space-between;
  }

  &__item,
  &__link {
    text-decoration: none;
    cursor: pointer;
    width: 100%;
    height: 100%;
    padding: 0 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0;
    color: rgba(#fff, 0.6);
    &:not(:last-child) {
      border-right: 1px solid var(--line-light);
    }

    &.active {
      background: var(--main-color);
      color: #fff;
      .mob-nav__icon {
        color: #fff;
        fill: #fff;
      }
    }
  }

  &__icon {
    color: rgba(#fff, 0.5);
    fill: rgba(#fff, 0.5);
  }

  &__item-name {
    font-size: 14px;
    font-weight: 600;
  }

  &__link {
    text-decoration: none;
    padding: 0;
  }
}
