.btn-favorite {
  text-decoration: none;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 44px;
  padding: 0 12px;
  color: var(--text-color);
  border: 1.4px solid var(--line-dark);
  border-radius: 8px;
  font-size: 15px;
  font-weight: 700;
  position: relative;
  transition: all 0.3s ease-in-out;

  &__label {
    margin-left: 8px;
  }

  &:hover {
    border: 1.4px solid var(--pink-color);
  }

  &::before {
    flex-shrink: 0;
    content: "";
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url("../img/icons/ic-favorite_no.svg") no-repeat center;
    background-size: cover;
  }

  &.active {
    &::before {
      background: url("../img/icons/ic-favorite_yes.svg") no-repeat center;
    }

    &:hover {
      background: transparent;
    }
  }
}
