#btnScrollTop {
  cursor: pointer;
  display: inline-block;
  width: 48px;
  height: 48px;
  color: #fff;
  background-color: #fff;
  box-shadow: 0px 8px 32px 0px rgba(77, 83, 103, 0.2);
  border-radius: 50%;
  position: fixed;
  bottom: 24px;
  right: 24px;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all 0.3s ease-in;

  &.show {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    &:hover {
      background-color: var(--pink-color);
      opacity: 1;

      &::before {
        background: url("../img/icons/ic-arrow_up-white.svg") no-repeat center;
      }
    }
  }

  // 
  &::before {
    content: "";
    flex-shrink: 0;
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url("../img/icons/ic-arrow_up-pink.svg") no-repeat center;
    background-size: cover;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease-in;
  }

  &:hover {
    opacity: 1;
  }

  @media (max-width: 767px) {
    right: 16px;
    bottom: 16px;
  }
}
