.page-nav {
  height: 56px;

  &__container {
    height: 100%;
    border-bottom: 1px solid var(--line-dark);
    padding: 0;
  }

  &__list {
    height: 100%;
    list-style: none;
    display: flex;
    justify-content: space-between;
  }

  &__item {
    width: 100%;
    height: 100%;
  }

  &__link {
    width: 100%;
    height: 100%;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    padding: 0 16px;
    color: var(--text-gray);
    font-size: 18px;
    font-weight: 400;
    position: relative;

    &:hover {
      color: var(--link-color);
    }

    &.active {
      cursor: default;
      text-decoration: none;
      color: var(--text-color);
      font-weight: 700;

      &:hover {
        color: var(--text-color);
      }

      &::after {
        content: " ";
        width: 100%;
        height: 3px;
        background: var(--blue-color);
        position: absolute;
        bottom: -2px;
        left: 0;
        transition: width 0.5s ease-in-out;
      }
    }

    .noti-indicator {
      position: relative;
      top: inherit;
      right: inherit;
      color: var(--system-color);
      font-weight: 800;
      background: none;
      border: 1px solid var(--line-dark);
      position: relative;
      left: 20px;
      &::before {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        background: var(--system-color);
        border-radius: 50%;
        position: absolute;
        top: 5px;
        left: -16px;
        animation: shadowIndicator 1.5s infinite alternate;
      }
    }
  }
}

@media (max-width: 480px) {
  .page__nav {
    height: 48px;
    word-wrap: nowrap;
    overflow-x: auto;
  }
}

@media (max-width: 390px) {
  .page-nav__link {
    font-size: 16px;
  }
}
