.modal-sendgift {
  .modal__content {
    max-width: 860px;
  }

  .modal__header {
    margin-bottom: 24px;
  }
  .modal__title {
    margin-bottom: 8px;
    span {
      font-size: 20px;
    }
  }
  .modal__descr {
    color: var(--text-gray);
  }

  &__body {
    display: grid;
    grid-template-columns: auto 220px;
    gap: 32px;
  }

  &__total {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__gift {
    width: 100%;
    aspect-ratio: 1 / 1;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__price {
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 0px 40px 0px rgba(73, 76, 86, 0.1);
    position: relative;
    padding: 16px 24px;
    &::before,
    &::after {
      content: "";
      width: 100%;
      height: 6px;
      background-image: url("../img/gift-tringle.svg");
      background-repeat: repeat-x;
      background-position: bottom;
      background-size: 12px 6px;
      position: absolute;
      left: 0;
    }

    &::before {
      top: -6px;
    }
    &::after {
      bottom: -6px;
      transform: rotate(-180deg);
    }
  }

  &__price-title {
    font-size: 24px;
    margin-bottom: 8px;
  }

  &__flower-total {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 8px;

    li {
      position: relative;
      &::before {
        width: 8px;
        height: 9px;
        background: var(--main-color);
        border-radius: 50%;
      }
    }
  }

  &__label {
    font-size: 15px;
    line-height: 1;
    margin-bottom: 12px;
  }
}

.quantity-flowers {
  &__block {
    display: flex;
    justify-content: space-between;
    gap: 16px;
  }

  &__list {
    display: flex;
    gap: 12px;
    list-style: none;
  }

  &__item {
    cursor: pointer;
    width: 40px;
    height: 40px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-weight: 600;
    line-height: 1;
    border: 1px solid var(--line-dark);
    border-radius: 6px;
    padding: 0px 12px;
    text-align: center;
    transition: all 0.3s ease-in-out;
    &:hover {
      border-color: var(--main-color);
    }
    &.active {
      color: #fff;
      background: var(--main-color);
      border-color: transparent;
      font-weight: 700;
    }
  }
}

.flowers-counter {
  height: 40px;
  display: inline-flex;
  justify-content: center;
  border: 1px solid var(--line-dark);
  border-radius: 6px;
  padding: 0;

  &:hover {
    border-color: var(--line-dark);
  }

  &__btn {
    cursor: pointer;
    flex-shrink: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 100%;
    &:hover {
      .flowers-counter__icon {
        color: var(--main-color);
      }
    }
  }

  &__icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    font-size: 20px;
    color: var(--text-color);
    transition: color 0.3s ease-in-out;
  }

  &__input {
    flex-shrink: 0;
    width: 40px;
    height: 100%;
    border: 1px solid var(--line-dark);
    border-top: none;
    border-bottom: none;
    padding: 0;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
  }

  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }
}

.flowers-colors {
  margin-top: 24px;

  &__list {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 24px;
  }

  &__item {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 16px;
    position: relative;
    z-index: 1;
    transition: all 0.2s ease-in-out;
    &:hover {
      color: var(--main-color);
    }
    &::before {
      content: "";
      width: 24px;
      height: 24px;
      background-color: #fff;
      border-radius: 50%;
      box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.1);
      border: 1.4px solid transparent;
      position: absolute;
      top: 50%;
      left: -4px;
      transform: translateY(-50%);
      z-index: -1;
      transition: border 0.2s ease-in-out;
    }

    &.active {
      font-weight: 700;
      &::before {
        border: 1.4px solid var(--main-color);
      }
    }
  }

  .color-item {
    display: inline-block;
    flex-shrink: 0;
    display: inline-block;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    position: relative;
    &::before {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: #fff;
      transform: translate(-50%, -50%);
      z-index: 1;
    }
  }
}

.form-sendgift {
  margin-top: 24px;

  &__label {
    margin-bottom: 8px;
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
  }

  &__btn {
    margin-left: auto;
  }
}

//  
.modal-sendgift_other {
  .modal-sendgift__gift {
    width: 180px !important;
    height: 180px !important;
    margin: 0 auto !important;
    margin-bottom: 24px !important;
  }

  .modal__header {
    text-align: center !important;
  }

  .form-sendgift__footer {
    .btn {
      width: 100%;
    }
  }

  .gift-price {
    font-size: 18px;
  }
}

@media (max-width: 800px) {
  .modal-sendgift__body {
    display: flex;
    flex-direction: column;
    gap: 0;
  }

  .modal-sendgift__total {
    order: -1;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .modal-sendgift__gift {
    width: 60%;
  }
}

@media (max-width: 600px) {
  .modal-sendgift__body {
    gap: 16px;
  }

  .modal-sendgift__total {
    margin-top: 24px;
  }

  .quantity-flowers__block {
    flex-direction: column;
  }

  .quantity-flowers__item {
    width: 100%;
  }
  .flowers-counter {
    // justify-content: space-between;
  }

  .flowers-counter__btn {
    width: 40px;
  }

  .flowers-counter__input {
    width: 80%;
  }
  .form-sendgift {
    margin-top: 16px;
  }

  .form-sendgift__btn {
    width: 100%;
  }

  .flowers-colors__list {
    flex-wrap: wrap;
    gap: 16px;
  }
}

@media (max-width: 480px) {
  .modal-sendgift .modal__title {
    text-align: center;
  }

  .modal-sendgift .modal__descr {
    display: none;
  }

  .modal-sendgift__body {
    gap: 32px;
  }
  .modal-sendgift__total {
    flex-direction: column;
    margin-top: 0;
  }

  .modal-sendgift__gift {
    width: 40%;
  }

  .modal-sendgift__price {
    padding: 16px;
    &-title {
      font-size: 18px;
    }
  }

  .modal-sendgift__flower-total {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 8px;
  }

  .form-sendgift__textarea {
    height: 80px;
  }
}

@media (max-width: 414px) {
  .flowers-counter__input {
    width: 75%;
  }
}

@media (max-width: 390px) {
  .modal-sendgift__price {
    padding: 8px 16px;
  }
}
