.tags-list {
  display: inline-flex;
  flex-wrap: wrap;
  gap: 8px;

  &__item {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    height: 32px;
    border: 1px solid var(--line-dark);
    border-radius: 4px;
    padding: 0 12px;
    font-size: 14px;
    font-weight: 500;
  }
}
