.page-tabs {
  &__header {
    display: flex;
    align-items: center;
    white-space: nowrap;
    overflow-x: auto;
    padding-bottom: 10px;
    position: relative;

    &::before {
      content: "";
      width: 100%;
      height: 1px;
      border-bottom: 1px solid var(--line-dark);
      position: absolute;
      bottom: 1px;
      left: 0;
    }
  }

  &__item {
    cursor: pointer;
    font-weight: 700;
    line-height: 1;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    text-decoration: none;
    padding: 8px 16px;
    transition: color 0.3s ease-in-out;
    position: relative;

    &::before {
      display: inline-block;
      content: "";
      width: 0;
      height: 3px;
      background: var(--main-color);
      position: absolute;
      bottom: -10px;
      left: 0;
      transition: width 0.3s ease-in-out;
    }

    &:hover {
      color: var(--main-color);

      &::before {
        display: none;
      }
    }

    &.active {
      cursor: default;
      color: var(--text-color);

      &::before {
        width: 100%;
      }

      &:hover::before {
        display: block;
        width: 100%;
      }
    }
  }
}
