.profile-info {
  display: flex;
  flex-direction: column;
  // gap: 24px;

  &__item {
    padding-left: 16px;
    border-left: 3px solid var(--pink-color);

    &:not(:last-child) {
      margin-bottom: 24px;
    }

    &.line-pink {
      border-left: 3px solid var(--pink-color);
    }

    &.line-blue {
      border-left: 3px solid var(--blue-color);
      .profile-info__read-more {
        color: var(--link-color);
      }
    }
  }

  &__item-header {
    margin-bottom: 8px;
  }

  &__item-title {
    color: var(--title-color);
    font-size: 18px;
    font-weight: 700;
  }

  .read-more {
    color: var(--pink-color);
    cursor: pointer;
    font-weight: 500;
    &:hover {
      text-decoration: underline;
    }
  }
}

.read-more {
  color: var(--pink-color);
  cursor: pointer;
  font-weight: 500;
  display: none;

  &.active {
    display: inline-block;
  }

  &:hover {
    text-decoration: underline;
  }
}
