.mail {
  position: relative;

  &__header {
    width: 100%;
    background: #fff;
    box-shadow: 0px 0px 40px 0px rgba(73, 76, 86, 0.1);
    padding: 12px 0;

    &.fixed {
      background: rgba(#fff, 0.86);
      backdrop-filter: blur(10px);
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 100;
    }

    &-container {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 16px;
    }
  }

  &__header-left {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__header-right {
    display: flex;
    align-items: center;
    gap: 12px;

    .mail-delete,
    .mail-print {
      flex-shrink: 0;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      cursor: pointer;
      .material-icons {
        color: var(--icon-gray);
      }

      .material-icons {
        transition: color 0.3s ease-in-out;
      }

      &:hover {
        .material-icons {
          color: var(--main-color);
        }
      }
    }

    .mail-delete:hover {
      .material-icons {
        color: var(--system-color);
      }
    }
  }

  &__header-profile {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__header-photo {
    cursor: pointer;
    text-decoration: none;
    display: inline-block;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    overflow: hidden;
    transition: opacity 0.3s ease-in-out;
    &:hover {
      opacity: 0.8;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__header-loc {
    font-size: 15px;
    .profile__location-icon {
      width: 18px;
      height: 18px;
      font-size: 18px;
    }
  }
}

// Mail Area
.mail-area {
  &__header {
    display: flex;
    align-items: center;
    gap: 9px;
    margin-bottom: 8px;
  }

  .ic-mail-send {
    display: inline-block;
    flex-shrink: 0;
    width: 20px;
    height: 20px;
  }

  &__label {
    font-weight: 700;
    span {
      font-weight: 400;
      color: var(--text-gray);
    }
  }

  &__field {
    width: 100%;
    height: 180px;
    position: relative;
    margin-bottom: 16px;
  }

  .mail-textarea {
    width: 100%;
    height: 100%;
    border-radius: 16px;
    background: #f8f8f8;
    padding: 16px;
    padding-right: 120px;
    &:focus {
      border-color: var(--blue-color);
      background: #fff;
    }
  }

  .upload-photo {
    cursor: pointer;
    width: 98px;
    height: 140px;
    border: 1px dashed #999;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
    text-align: center;
    font-size: 14px;
    line-height: 1.4;
    padding: 8px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 16px;
    transition: all 0.3s ease-in-out;
    &:hover {
      border-color: var(--blue-color);
      .upload-photo__icon {
        background: var(--blue-color);
        border-color: transparent;
        span {
          color: #fff;
        }
      }
    }

    &__icon {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      border: 1px solid rgba(#000, 0.15);
      border-radius: 50%;
      transition: all 0.3s ease-in-out;
      span {
        color: var(--icon-gray);
      }
    }

    &__text {
      font-size: 12px;
      color: var(--text-gray);
    }
  }

  &__footer {
    // display: flex;
    // justify-content: space-between;
    display: grid;
    grid-template-columns: auto 220px;
    gap: 16px;
  }

  &__info {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 15px;
    .material-icons {
      color: var(--icon-gray);
      width: 20px;
      height: 20px;
      font-size: 20px;
      opacity: 0.8;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__btn {
    flex-shrink: 0;
    height: 48px;
    padding: 0 24px;
    font-size: 15px;
    .btn__icon {
      width: 20px;
      height: 20px;
      font-size: 20px;
    }
  }

  .btn-chatsmile {
    flex-shrink: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    border: 1px solid var(--line-dark);
    border-radius: 8px;
    background-size: 24px 24px;
  }
}

// Mail Letters
.mail-letters {
  padding-bottom: 32px;
}

@media (max-width: 1024px) {
  .mail-area .btn-chatsmile {
    display: none;
  }

  .mail-area__footer {
    display: flex;
    justify-content: space-between;
    gap: 16px;
  }
}

@media (max-width: 640px) {
  .mail__header {
    .profile-id {
      display: none;
    }
  }

  .mail__header-info {
    p {
      font-size: 14px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media (max-width: 480px) {
  .mail__header {
    padding: 8px 0;
  }

  .mail__header-correspondence {
    display: none;
  }

  .mail__header-loc {
    .city {
      display: none;
    }
  }

  .mail-area .mail-textarea {
    border-radius: 8px;
  }

  .mail-area__footer {
    flex-direction: column;
  }

  .mail-letters {
    padding-bottom: 24px;
  }
}

@media (max-width: 390px) {
  .mail-area__header {
    flex-wrap: wrap;
  }
  .mail-area__label {
    font-size: 14px;
  }

  .mail-area .mail-textarea {
    margin-bottom: 8px;
  }
}
