.profile-banner {
  width: 100%;
  display: flex;
  align-items: center;
  // gap: 16px;
  border-radius: var(--br-16);
  background: var(--blue-color);
  padding: 16px 24px;
  position: relative;
  overflow: hidden;
  z-index: 1;

  &:not(:last-child) {
    margin-bottom: 16px;
  }

  &:hover {
    .btn {
      animation: pulseAnim 1.5s linear infinite;
    }
  }

  &__icon {
    flex-shrink: 0;
    transform: rotate(-15deg);
    position: relative;
    top: 8px;
  }

  &__body {
    width: 100%;
  }

  &__title {
    color: #fff;
    font-size: 24px;
    line-height: 1.2;
    font-weight: 700;
  }

  &__descr {
    color: #fff;
    font-size: 18px;
  }

  .chat-form {
    margin-top: 16px;
  }

  &.banner-chat {
    background: var(--blue-color);
  }
}

.banner-chat {
  &--mob {
    display: flex;
  }

  &::after {
    content: "";
    display: inline-block;
    width: 176px;
    height: 176px;
    background: url("../img/banner-chat_heart.svg") no-repeat center;
    background-size: cover;
    position: absolute;
    top: 8px;
    right: 16px;
    opacity: 0.16;
    z-index: -1;
  }
}

.banner-chat .chat-history {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  display: none;

  &.active {
    display: flex;
  }

  .profile-banner__title {
    font-size: 32px;
  }

  .chat-item {
    cursor: pointer;
    width: 60%;
    display: block;
    transition: all 0.3s ease-in-out;

    &:hover {
      transform: scale(1.02);
    }

    &__wrapper {
      width: 100%;
      max-width: inherit;
      min-width: inherit;
      background: #fff !important;
    }
  }
}

.banner-chat .chat-history__item--man {
  .chat-item__wrapper {
    border-radius: 16px 0 16px 16px;
  }
  .chat-item__profile {
    color: var(--blue-color);
  }

  .readmore__icon {
    color: var(--blue-color);
    fill: var(--blue-color);
  }
}

.banner-chat .chat-history__item--woman {
  .chat-item__wrapper {
    border-radius: 0px 16px 16px 16px;
  }
  .chat-item__profile {
    color: var(--pink-color);
  }
  .readmore__icon {
    color: var(--blue-color);
    fill: var(--blue-color);
  }
}

/* ------------------- Banner Gift -------------------- */
.banner-gift {
  background: linear-gradient(92deg, #ffdbdb 0.11%, #edeeff 100%);

  &::after {
    content: "";
    display: inline-block;
    width: 176px;
    height: 176px;
    background: url("../img/banner-gift_heart.svg") no-repeat center;
    background-size: cover;
    position: absolute;
    top: 8px;
    right: 16px;
    opacity: 0.16;
    z-index: -1;
  }

  .profile-banner__icon {
    transform: rotate(-10deg);
    position: relative;
    top: -2px;
    margin-right: 16px;
  }

  .profile-banner__title {
    color: var(--text-color);
    font-size: 24px;
    margin-bottom: 8px;
  }

  .profile-banner__descr {
    color: #404040;
    font-size: 18px;
  }

  .btn {
    flex-shrink: 0;
    padding: 12px 24px;
    box-shadow: var(--bx-pink);
    &:hover {
      transform: none;
    }
  }
}

.banner-gift__left {
  width: 100%;
  display: flex;
  align-items: center;
  // gap: 24px;
  margin-right: 24px;
}

/* ------------------- banner-comunications -------------------- */
.banner-comunications {
  flex-direction: column;
  align-items: center;
  background: linear-gradient(
    92deg,
    #d7f2ff 0.76%,
    #a4dfff 0.77%,
    #dffff3 100%
  );
  padding: 24px 16px;

  &::before {
    content: "";
    display: inline-block;
    width: 222px;
    height: 222px;
    background: url("../img/banner-comunication_chat.svg") no-repeat center;
    background-size: cover;
    position: absolute;
    top: -48px;
    left: -48px;
    opacity: 0.32;
    z-index: -1;
  }

  &:hover {
    .btn {
      animation: none;
    }
  }

  .profile-banner__title {
    text-align: center;
    color: var(--text-color);
    margin-bottom: 24px;
  }

  .profile-banner__descr {
    color: #404040;
  }

  &__btns {
    display: flex;
    align-items: center;
    // gap: 16px;

    .btn {
      &:not(:last-child) {
        margin-right: 16px;
      }
    }
  }

  .btn-icon {
    width: 20px;
    height: 20px;
    font-size: 20px;
  }

  .mail-link {
    border: none;
    background: var(--blue-gradient);
  }

  .chat-link {
    border: none;
    background: var(--chat-gradient);
    .btn-icon {
      position: relative;
      top: 2px;
    }
  }
}

@media (max-width: 1024px) {
  .profile-banner.banner-chat {
    padding: 16px;
  }

  .banner-chat .chat-history {
    flex-direction: column;
    align-items: flex-start;
  }

  .banner-chat .chat-history .chat-item {
    width: 100%;
  }

  .banner-chat .chat-history .profile-banner__title {
    font-size: 24px;
  }

  .banner-chat .profile-banner__icon img {
    width: 128px;
    height: 128px;
  }

  .banner-gift .profile-banner__title,
  .banner-comunications .profile-banner__title {
    font-size: 20px;
    line-height: 1.4;
  }

  .banner-gift .profile-banner__descr {
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  .banner-chat .chat-history {
    flex-direction: row;
    align-items: center;
  }

  .banner-chat .chat-history .profile-banner__title {
    font-size: 32px;
  }

  .banner-gift .profile-banner__title,
  .banner-comunications .profile-banner__title {
    font-size: 24px;
    line-height: 1.2;
  }

  .banner-chat .chat-history .chat-item {
    width: 50%;
  }
}

@media (max-width: 767px) {
  .profile-banner__icon {
    display: none;
  }
}

@media (max-width: 600px) {
  .profile-banner {
    text-align: center;
  }

  .banner-chat .chat-history {
    flex-direction: column;
    justify-content: flex-start;
  }

  .banner-chat .chat-history .chat-item {
    width: 100%;
  }

  .banner-chat .chat-history .chat-item__body {
    text-align: left !important;
  }

  .banner-gift {
    flex-direction: column;
    padding-bottom: 24px;
  }

  .banner-gift__left {
    flex-direction: column;
    gap: 0;
  }

  .banner-gift .profile-banner__icon {
    img {
      width: 80px;
      height: 80px;
    }
  }

  .banner-gift__left {
    margin-right: 0;
  }

  .banner-gift .profile-banner__body {
    margin-bottom: 16px;
  }

  .banner-comunications .profile-banner__title {
    font-size: 22px;
    line-height: 1.4;
  }
}

@media (max-width: 480px) {
  .profile-banner {
    padding: 16px 20px;
  }
  .profile-banner__title {
    font-size: 20px;
  }

  .profile-banner:hover .btn {
    animation: none;
  }

  .banner-comunications .profile-banner__title {
    font-size: 20px;
    margin-bottom: 16px;
  }

  .banner-comunications__btns {
    width: 100%;
    flex-direction: column;
    gap: 12px;

    .btn {
      width: 100%;
      height: 48px;
      padding: 0 16px;

      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }
}

@media (max-width: 390px) {
  .banner-chat .chat-history .profile-banner__title {
    font-size: 24px;
  }
}
