.popup-like {
  width: 288px;
  position: absolute;
  top: 16px;
  right: 0;
  // display: none;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 20;
  transition: all 0.2s ease-in-out;

  @media (max-width: 992px) {
    top: 5%;
  }

  &.fixed {
    position: fixed;
    top: 80px;
    right: 16px;
  }

  &.show {
    right: 16px;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    animation: var(--animation-fade);
  }

  &__body {
    display: flex;
    align-items: center;
    gap: 16px;
    min-width: 280px;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(4px);
    box-shadow: var(--bx-popup);
    border-radius: var(--br-12);
    padding: 16px;
    position: relative;
  }

  &__close {
    @extend .modal__close;
    width: 14px !important;
    height: 14px !important;
    top: 10px !important;
    right: 10px !important;
  }

  .page-ava {
    position: relative;
    z-index: 0;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      background: rgba(#000, 0.2);
      backdrop-filter: blur(2px);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
    }
  }

  &__icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    background: var(--pink-color);
    border: 1px solid #fff;
    border-radius: 50%;
    color: #fff;
    position: absolute;
    bottom: 12px;
    left: 40px;
    z-index: 1;

    .material-icons {
      display: inline-block;
      width: 16px;
      height: 16px;
      font-size: 16px;
    }
  }
}
