.send-gifts {
  &__header {
    display: grid;
    grid-template-columns: 134px auto;
    gap: 24px;
    margin-bottom: 32px;
  }

  .profile__photo {
    width: 100%;
    height: 204px;
    display: block !important;
    border-radius: 12px;
    margin-bottom: 0;
    .profile-status {
      width: 16px;
      height: 16px;
      top: 8px;
      right: 8px;
    }
  }

  .profile__header {
    display: flex !important;
  }

  .profile__name {
    font-size: 24px;
  }

  .profile__location {
    font-size: 16px;
  }

  .chat-form__input {
    border: 1px solid var(--line-dark);
  }

  .chat-form__btn {
    width: 110px;
    gap: 8px;
    padding: 0 24px;
  }

  .photo-empty {
    border-radius: 12px;
    border-width: 1.5px;
  }

  .photo-empty__center {
    gap: 8px;
  }

  .photo-empty__icon {
    width: 48px;
    height: 48px;
  }

  .photo-empty__label {
    font-size: 16px;
  }

  &__title {
    margin-bottom: 0;
    span {
      color: var(--text-gray);
      font-size: 16px;
      font-weight: 400;
    }
  }

  &__select {
    max-width: 280px;
  }
}

// Content
.send-gifts__content {
  &-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    margin-bottom: 32px;
  }

  .send-gifts__title {
    margin: 0;
  }
}

// Gifts
.gifts {
  padding-top: 32px;
  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    column-gap: 16px;
    row-gap: 24px;

    @media (max-width: 480px) {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 8px;
      row-gap: 16px;
    }
  }

  .pagination {
    padding-bottom: 0;
  }
}

@media (max-width: 800px) {
  .send-gifts {
    .profile__options-label,
    .btn-favorite__label {
      display: none;
    }

    .profile__options-btn,
    .btn-favorite {
      width: 48px;
      height: 48px;
      padding: 0;
      justify-content: center;
      align-items: center;
      border: 1.4px solid var(--main-color);
    }

    .btn-favorite {
      border-color: var(--line-dark);
      &.active {
        border-color: var(--main-color);
      }
    }

    .send-gifts__title {
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 8px;
    }
  }
}

@media (max-width: 640px) {
  .send-gifts__content-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    margin-bottom: 24px;
  }

  .send-gifts__select {
    max-width: 100%;
  }

  .send-gifts .btn-chat {
    width: 48px;
  }

  .send-gifts .btn-chat__label {
    display: none;
  }
}

@media (max-width: 599px) {
  .send-gifts .profile__header {
    justify-content: center;
    gap: 12px;
  }

  .send-gifts__header {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .send-gifts .profile__photo {
    width: 140px;
  }

  .send-gifts__header-right {
    width: 100%;
  }
}

@media (max-width: 390px) {
  .send-gifts .profile__photo {
    width: 140px;
  }

  .send-gifts .profile__name {
    font-size: 20px;
  }
}
