.noti-indicator {
  flex-shrink: 0;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 20px;
  padding: 0 4px;
  color: #fff;
  background: var(--pink-color);
  border-radius: 100px;
  font-size: 11px;
  font-weight: 900;
  line-height: 1;
  position: absolute;
  top: -8px;
  right: -8px;

  &--gray {
    background: rgba(255, 255, 255, 0.2);
  }

  &--circle {
    width: 22px;
    height: 22px;
  }
}

@keyframes shadowIndicator {
  0% {
    transform: scale(0.4);
    box-shadow: 0px 0px 8px rgba(235, 0, 27, 0);
  }

  100% {
    transform: scale(1);
    box-shadow: 0px 0px 8px rgba(235, 0, 27, 0.8);
  }
}
