@charset "UTF-8";
html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

body {
  margin: 0;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}

button,
input {
  /* 1 */
  overflow: visible;
}

button,
select {
  /* 1 */
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/* ------------------- Lato Font -------------------- */
@font-face {
  font-family: "Lato";
  src: local("Lato Regular"), local("Lato-Regular"), url("../fonts/lato-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("Lato Italic"), local("Lato-Italic"), url("../fonts/lato-italic.woff2") format("woff2");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("Lato Medium"), local("Lato-Medium"), url("../fonts/lato-medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("Lato Semibold"), local("Lato-Semibold"), url("../fonts/lato-semibold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("Lato Bold"), local("Lato-Bold"), url("../fonts/lato-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: local("Lato Black"), local("Lato-Black"), url("../fonts/lato-black.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
:root {
  --wrapper-width: 1920px;
  --container-width: 1200px;
  --font-default: "Lato", sans-serif;
  --fz-body: 16px;
  --lh-body: 1.5;
  --body-bg: #fff;
  --dark-color: #212121;
  --white-color: #fff;
  --dark-color: #212121;
  --main-color: #e65b7e;
  --pink-color: #e65b7e;
  --blue-color: #2baeec;
  --purple-color: #9b51e0;
  --yellow-color: #ffb020;
  --green-color: var(--success-color);
  --red-color: var(--error-color);
  --chat-gradient: linear-gradient(180deg, #26cd6f 0%, #13b19f 100%);
  --blue-gradient: linear-gradient(96deg, #45bdf0 -1.55%, #3076de 96.72%);
  --link-color: #2d8fbe;
  --pink-light-color: #f8eaee;
  --blue-light-color: #e5f2f9;
  --purple-light-color: #f1e9f7;
  --yellow-light-color: #fbf3e4;
  --green-light-color: #e7f5e8;
  --error-light-color: #fbedec;
  --panel-nav-color: #2f3945;
  --error-color: #ca4040;
  --system-color: #ca4040;
  --warning-color: #ffb020;
  --success-color: #23c554;
  --gray-color_1: #e0e0e0;
  --gray-color_2: #f2f2f2;
  --gray-color_3: #fafafa;
  --title-color: #212121;
  --text-color: #333;
  --text-gray: #828282;
  --icon-gray: #bdbdbd;
  --line-dark: rgba(0, 0, 0, 0.15);
  --line-light: rgba(255, 255, 255, 0.12);
  --btn-border-color: #d0d0d0;
  --field-placeholder: rgba(0, 0, 0, 0.4);
  --bg-box: var(--white-color);
  --br-box: var(--br-16);
  --border-box: 1px solid var(--line-dark);
  --bx-dropdown: 0px 8px 32px 0px rgba(77, 83, 103, 0.16);
  --bx-modal: 0 5px 32px rgba(77, 83, 103, 0.2);
  --bx-popup: 0px 8px 32px 0px rgba(77, 83, 103, 0.2);
  --bx-pink: 0px 8px 16px 0px rgba(230, 91, 126, 0.4);
  --bx-blue: 0px 8px 16px 0px rgba(43, 174, 236, 0.4);
  --overlay-modal: rgba(0, 0, 0, 0.5);
  --br-32: 32px;
  --br-24: 24px;
  --br-16: 16px;
  --br-12: 12px;
  --br-8: 8px;
  --br-6: 6px;
  --br-4: 4px;
  --header-height: 72px;
  --nav-width: 296px;
  --animation-fade: fade 0.3s ease-in-out;
}

/* ------------------- Default -------------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::before, *::after {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  background: var(--body-bg);
  color: var(--text-color);
  font-family: var(--font-default);
  font-size: var(--fz-body);
  line-height: var(--lh-body);
  color: var(--text-color);
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page__body.lock-scroll {
  overflow: hidden;
}

.page__wrapper {
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.page__main {
  flex: 1;
}

.footer {
  margin-top: auto;
}

.hidden {
  display: none !important;
}

.container {
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 16px;
}
@media (max-width: 1024px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 599px) {
  .container {
    padding: 0 16px;
  }
}

.page-padding {
  padding-top: clamp(1.5rem, 1.318rem + 0.91vw, 2rem);
  padding-bottom: clamp(3rem, 2.636rem + 1.82vw, 4rem);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--title-color);
}

.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5,
.title-h6 {
  color: var(--title-color);
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  cursor: pointer;
  font-size: inherit;
  color: inherit;
  transition: all 0.2s ease-in-out;
}
a:hover {
  text-decoration: none;
}

input,
button,
textarea,
select {
  font-family: var(--font-default);
}

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  background-color: none;
  font-size: inherit;
}

input,
select {
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  outline: none;
  border-radius: 0;
  padding: 0;
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  outline: none;
  color: inherit;
}
input::placeholder,
select::placeholder {
  color: #666;
}

select::-ms-expand {
  display: none;
}

strong,
.strong {
  font-weight: bold;
  font-weight: 700;
}

.list-reset {
  list-style: none;
}

.text-center {
  text-align: center;
}

.text-number {
  color: var(--text-gray);
  font-weight: 400;
}

.hidden {
  display: none;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.text-date {
  font-weight: 400;
  color: var(--text-gray);
}

.new-counter {
  font-weight: 700 !important;
  color: var(--pink-color) !important;
}

.flex {
  display: flex;
}
.flex.justify-sb {
  justify-content: space-between;
}
.flex.justify-center {
  justify-content: center;
}
.flex.align-center {
  align-items: center;
}

.material-icons {
  transition: color 0.3s ease-in-out;
}

