.uikit-block {
  width: 100%;
  max-width: 960px;
  margin: auto;
}
.uikit-block__header {
  background: var(--dark-color);
  display: flex;
  align-items: center;
  padding: 32px;
}
.uikit-block__title {
  font-size: 48px;
  color: #fff;
}
.uikit-block__body {
  padding: 64px 32px;
}
.uikit-block .text-container div {
  margin-bottom: 24px;
}

.uikit-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  flex-wrap: wrap;
}
.uikit-grid .uikit-block__body div:not(:last-child) {
  margin-bottom: 0;
}

.uikit-item {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.uikit-item__box {
  width: 100%;
  height: 104px;
  border-radius: var(--br-16);
}
.uikit-item__box:not(:last-child) {
  margin-bottom: 0 !important;
}
.uikit-item__info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.datepicker {
  padding-left: 12px 10px;
}

.datepicker-global-container {
  z-index: 10000001;
}

.datepicker-nav--title {
  font-weight: 500;
}

.datepicker--day-name {
  color: var(--blue-color);
  font-family: var(--font-default);
  font-weight: 700;
}

.datepicker--cell.-current- {
  color: var(--blue-color);
  font-weight: 700;
}

.datepicker--cell.-selected-,
.datepicker--cell.-selected-.-current-,
.datepicker-cell.-selected- {
  background: var(--blue-color);
}

.datepicker--button,
.datepicker-cell.-current- {
  color: var(--blue-color);
  background-color: #f2f2f2;
}

.datepicker-inline.hide {
  display: none;
  animation: var(--animation-fade);
}

.swiper-inner {
  width: 100%;
  position: relative;
}

.swiper-container {
  max-width: var(--container-width);
  height: auto;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.swiper-wrapper {
  box-sizing: content-box;
  max-width: var(--container-width);
  height: auto;
  display: flex;
}

.swiper-slide {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.swiper-prev,
.swiper-next {
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #fff;
  border: 2px solid transparent;
  box-shadow: var(--bx-popup);
  transition: all 0.3s ease-in-out;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  display: inline-flex !important;
}
.swiper-prev .material-icons,
.swiper-next .material-icons {
  color: var(--main-color);
}
.swiper-prev:hover,
.swiper-next:hover {
  color: #fff;
  background: var(--main-color);
  border: 2px solid #fff;
  box-shadow: var(--bx-pink);
}
.swiper-prev:hover .material-icons,
.swiper-next:hover .material-icons {
  color: #fff;
}

.swiper-prev {
  left: -24px;
}
.swiper-prev .material-icons {
  width: 18px;
  height: 18px;
  font-size: 18px;
  position: relative;
  left: 4px;
}

.swiper-next {
  right: -24px;
}

.swiper-pagination {
  position: absolute !important;
  top: inherit !important;
  right: inherit !important;
  bottom: -32px !important;
  left: 50% !important;
  transform: translateX(-50%) !important;
  z-index: 1 !important;
}

.swiper-pagination-bullet {
  width: 8px !important;
  height: 8px !important;
  opacity: 0.15 !important;
}

.swiper-pagination-bullet-active {
  background: var(--main-color) !important;
  opacity: 1 !important;
}

.slider-profiles-two .swiper-prev,
.slider-profiles-two .swiper-next {
  display: none !important;
}
.slider-profiles-two .swiper-pagination {
  bottom: -24px !important;
}

.slider-profiles-more .swiper-pagination {
  display: none;
}
@media (max-width: 1024px) {
  .slider-profiles-more .swiper-pagination {
    display: block !important;
  }
}
@media (max-width: 1024px) {
  .slider-profiles-more .swiper-prev,
.slider-profiles-more .swiper-next {
    display: none !important;
  }
}

.profile-slider-inner {
  position: relative;
  overflow: hidden;
}
.profile-slider-inner .swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 56px !important;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
  bottom: 0 !important;
  z-index: 10;
}
.profile-slider-inner .swiper-pagination-bullet {
  background: #fff !important;
  opacity: 0.3 !important;
}
.profile-slider-inner .swiper-pagination-bullet-active {
  width: 12px !important;
  height: 12px !important;
  background: var(--main-color) !important;
  border: 2px solid #fff;
  opacity: 1 !important;
}
.profile-slider-inner .profile-slider {
  display: flex;
  height: 100%;
}
.profile-slider-inner .swiper-slide {
  height: 100%;
}

.my-gallery {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000000;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s;
  pointer-events: none;
}
.my-gallery.active {
  display: flex;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.my-gallery__overlay {
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  display: flex;
  padding: 5%;
}
.my-gallery__close {
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  transition: background-color 0.3s ease-in-out;
}
.my-gallery__close .material-icons {
  display: inline-block;
  width: 24px;
  height: 24px;
  color: #fff;
  font-size: 24px;
  z-index: 1;
}
.my-gallery__close:hover {
  background: var(--dark-color);
}
.my-gallery__prev, .my-gallery__next {
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: background-color 0.3s ease-in-out;
  z-index: 1;
}
.my-gallery__prev .material-icons, .my-gallery__next .material-icons {
  display: inline-block;
  width: 24px;
