.modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000000;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s;
  pointer-events: none;
}
.modal.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.modal.active .modal__content {
  transform: translateY(0);
}
.modal__overlay {
  width: 100%;
  height: 100%;
  background-color: var(--overlay-modal);
  display: flex;
  padding: 20px;
}
