}
.modal__content {
  width: 100%;
  max-height: 90%;
  max-width: 640px;
  margin: auto;
  overflow: auto;
  background: #fff;
  border-radius: 16px;
  transform: translateY(-100px);
  padding: 32px;
  transition: transform 0.3s ease-in-out;
  position: relative;
}
@media (max-width: 480px) {
  .modal__content {
    max-height: 95%;
    padding: 24px 20px;
  }
}
.modal__close, .system-message__close, .popup-like__close {
  cursor: pointer;
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("data:image/svg+xml;base64,");
  background-size: cover;
  position: absolute;
  top: 16px;
  right: 16px;
  opacity: 0.3;
  transition: all 0.3s ease-in-out;
}
.modal__close:hover, .system-message__close:hover, .popup-like__close:hover {
  background: url("data:image/svg+xml;base64,");
  opacity: 1;
}
