  background-size: 16px 16px;
  border: 1.4px solid #d0d5dd;
}
.radio__input:checked:disabled + .radio__box::before {
  background-color: #d0d5dd;
}

.toogle {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  position: relative;
  vertical-align: middle;
  font-size: 14px;
  user-select: none;
}
.toogle__input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: none;
  border: none;
  outline: none;
}
.toogle__switch {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 40px;
  height: 22px;
  border: 1px solid transparent;
  border-radius: 100px;
  background: var(--gray-color_1);
  transition: 0.2s;
  position: relative;
}
.toogle__switch:before {
  content: "";
  position: absolute;
  top: 1px;
  left: 2px;
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: white;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.12);
  transition: 0.15s;
}

.toogle__input:checked + .toogle__switch {
  background: var(--main-color);
}

.toogle__input:checked + .toogle__switch:before {
  transform: translateX(16px);
  top: 1px;
}

/* Hover */
.toogle__input:not(:disabled) + .toogle__switch {
  cursor: pointer;
}

/* Disabled */
.toogle__input:disabled + .toogle__switch {
  filter: grayscale(70%);
  cursor: default;
}

.toogle__input:disabled + .toogle__switch:before {
  background: #eee;
  cursor: default;
}

/* Focus */
.toogle.focused .toogle__switch:before {
  box-shadow: inset 0px 0px 4px var(--main-color);
}

.chat-form {
  width: 100%;
}
.chat-form__field {
  position: relative;
}
.chat-form__input {
  height: 56px !important;
  border: none;
  padding-right: 96px;
}
.chat-form__input:focus {
  border: none;
}
.chat-form__btn {
  width: 44px;
  height: 44px;
  position: absolute;
  top: 4px;
  right: 4px;
  color: #fff;
}
.chat-form__btn-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  font-size: 20px;
  color: #fff;
  position: relative;
  left: 2px;
}
.chat-form .btn-chatsmile {
  position: absolute;
  top: 50%;
  right: 64px;
  transform: translateY(-50%);
}

/* ------------------- Animation -------------------- */
.anim-pulse {
  animation: pulseAnim 2s linear infinite;
}

.anim-rotate {
  animation: rotateAnim 1.5s linear infinite;
}

@keyframes pulseAnim {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes rotateAnim {
  0% {
    transform: rotate(-15deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-15deg);
  }
}
