  padding: 12px 0;
}
.mail__header.fixed {
  background: rgba(255, 255, 255, 0.86);
  backdrop-filter: blur(10px);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.mail__header-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}
.mail__header-left {
  display: flex;
  align-items: center;
  gap: 16px;
}
.mail__header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}
.mail__header-right .mail-delete,
.mail__header-right .mail-print {
  flex-shrink: 0;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  cursor: pointer;
}
.mail__header-right .mail-delete .material-icons,
.mail__header-right .mail-print .material-icons {
  color: var(--icon-gray);
}
