.mail__header-right .mail-print .material-icons {
  transition: color 0.3s ease-in-out;
}
.mail__header-right .mail-delete:hover .material-icons,
.mail__header-right .mail-print:hover .material-icons {
  color: var(--main-color);
}
.mail__header-right .mail-delete:hover .material-icons {
  color: var(--system-color);
}
