.back-page {
  text-decoration: none;
  cursor: pointer;
  width: 32px;
  height: 32px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  .material-icons {
    color: var(--text-color);
    display: inline-block;
    width: 32px;
    height: 32px;
    font-size: 32px;
    transition: color 0.3s ease-in-out;
  }

  &:hover {
    .material-icons {
      color: var(--main-color);
    }
  }
}

@media (max-width: 640px) {
  .back-page {
    display: none;
  }
}
