.profile {
  position: relative;
  .container {
    position: relative;
  }

  &__body {
    display: grid;
    grid-template-columns: 380px auto;
    gap: 32px;
  }
}

/* ------------------- Left Column -------------------- */
.profile {
  &__photos {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__photo {
    width: 100%;
    height: 524px;
    // aspect-ratio: 2 / 3;
    border-radius: var(--br-16);
    position: relative;
    overflow: hidden;
    z-index: 0;
    margin-bottom: 16px;

    .profile-status {
      width: 20px;
      height: 20px;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
      position: absolute;
      top: 16px;
      right: 16px;
    }

    &-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
  }

  &__status {
    width: 100%;
    max-width: max-content;
    position: absolute;
    bottom: 24px;
    left: 50%;
    transform: translateX(-50%);

    &-text {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      width: 100%;
      color: #fff;
      border-radius: 8px;
      background: rgba(0, 0, 0, 0.6);
      backdrop-filter: blur(8px);
      padding: 8px 20px;
    }
  }

  .left-block {
    padding: 16px;
    border-radius: 16px;
    border: 1px solid var(--line-dark);

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &__title {
      margin-bottom: 16px;
      display: flex;
      align-items: baseline;
      // gap: 8px;
      line-height: 1;
      span {
        font-size: 16px;
        font-weight: 600;
        color: var(--text-gray);
        margin-left: 8px;
      }
    }

    &__grid {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 12px;
    }
  }

  .show-videos {
    display: flex;
    justify-content: flex-end;
    margin-top: 8px;
    span {
      cursor: pointer;
      color: var(--main-color);
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.profile__videos {
  .video-item {
    border-radius: 8px;
  }
}

.profile__photo-item {
  // aspect-ratio: 2 / 3;
  width: 100%;
  cursor: pointer;
  display: block;
  transition: all 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
  border-radius: 8px;

  &:hover {
    opacity: 0.8;
    transform: scale(1.02);
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.profile__videos-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;

  @media (max-width: 992px) {
    grid-template-columns: repeat(4, 1fr);
  }

  @media (max-width: 800px) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.profile__interview {
  .read-more {
    color: var(--blue-color);
  }
  .read-more__icon {
    color: var(--blue-color);
    fill: var(--blue-color);
  }
  &-btn {
    width: 100%;
    margin-top: 24px;
  }
}

.profile__populars-ladies {
  padding-bottom: 32px !important;
}

/*------------------- Right Column -------------------- */
.profile {
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }

  &__options {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 12px;
  }

  &__options-item {
    &.active {
      color: #fff;
      background: var(--main-color);
    }
  }

  &__options-btn {
    text-decoration: none;
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 44px;
    padding: 0 16px;
    color: var(--text-color);
    border: 1.4px solid transparent;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 700;
    transition: all 0.3s ease-in-out;

    &:hover {
      background: var(--pink-light-color);
      border: 1.4px solid var(--pink-color);
    }

    &.active {
      cursor: default;
      color: #fff;
      background: var(--main-color);
      box-shadow: var(--bx-pink);

      &:hover {
        opacity: 1 !important;
        animation: none !important;
      }

      .profile__options-icon {
        fill: #fff;
      }

      &:hover {
        animation: pulseAnim 1.5s linear infinite;
        opacity: 0.8;
      }
    }
  }

  &__options-icon {
    flex-shrink: 0;
    width: 22px;
    height: 22px;
    fill: var(--main-color);
  }

  &__options-label {
    margin-left: 8px;
  }
}

.profile__top-info {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .left {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .right {
    align-self: flex-start;
  }
}

.profile__name {
  @extend .page-title;
  margin-top: 0;
  margin-bottom: 0;
}

.profile__location {
  gap: 4px;
  color: var(--text-gray);
  font-size: 18px;

  &-icon {
    width: 20px;
    height: 20px;
    font-size: 20px;
    color: var(--main-color);
  }
}

.profile__looking {
  display: flex;
  align-items: center;
  gap: 16px;
}

.profile__more-videos {
  margin-top: 12px;
  display: none;
  animation: var(--animation-fade);

  &.show {
    display: block;
  }

  .pagination {
    padding: 0;
    padding-top: 16px;
    justify-content: center;
  }
}

/* ------------------- Mobile Blocks max-width: 992px -------------------- */
.mob-profilephotos {
  width: 100%;
  height: 540px;
  margin-top: -68px;
  position: relative;
  overflow: hidden;
  display: none;

  @media (max-width: 992px) {
    display: block;
  }

  @media (max-width: 480px) {
    height: 480px;
  }

  .photo-empty {
    border-radius: 0;
    border: none;
    background: var(--gray-color_2);
    position: relative;
    &::before {
      content: "";
      width: 100%;
      height: 68px;
      background: var(--dark-color);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
    }
  }
}

.profile-moboptions {
  position: absolute;
  bottom: 48px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  transition: all 0.3s ease-in-out;
  display: none;

  @media (max-width: 480px) {
    bottom: 40px;
  }

  @media (max-width: 992px) {
    display: block;
  }

  &.scrolled {
    position: fixed;
    left: inherit;
    transform: none;
    bottom: inherit;
    top: 25%;
    right: 16px;
    .profile-moboptions__list {
      flex-direction: column;

      .item-gift,
      .item-contacts {
        display: none;
      }
    }
  }

  &__list {
    list-style: none;
    display: flex;
    gap: 12px;
    transition: flex 0.3s ease-in-out;
  }

  &__btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(4px);
    border-radius: 50%;

    &.active {
      background: var(--pink-color);
      box-shadow: var(--bx-pink);
      backdrop-filter: none;
    }
  }

  &__icon {
    width: 22px;
    height: 22px;
    color: #fff;
    fill: #fff;
  }

  .btn-favorite {
    &::before {
      background: url("../img/icons/ic-favorite_no-white.svg") no-repeat center;
    }

    &:hover {
      background: rgba(0, 0, 0, 0.6);
      backdrop-filter: blur(4px);
      border: none;
    }

    &.active {
      background: #fff;
      box-shadow: 0px 8px 32px 0px rgba(77, 83, 103, 0.2);
      border: none;
      // box-shadow: none;
      // backdrop-filter: blur(4px);

      &::before {
        background: url("../img/icons/ic-favorite_yes.svg") no-repeat center;
      }
    }
  }
}

.profile .profile-slider-inner {
  width: 100%;
  max-width: 100%;
  height: 100%;
  overflow: hidden;
}

.profile .mob-photo {
  width: 100%;
  height: 100%;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 0;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.7) 0%,
      rgba(0, 0, 0, 0) 22.63%
    );
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 0;
  }

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(#fff, 0.2);
    backdrop-filter: blur(16px);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  &__img {
    display: inline-block;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &--vert {
    .mob-photo__img {
      width: 375px;
    }
  }

  &--hor {
    &::after {
      display: none;
    }
  }
}

.profile .mob-slide {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  text-align: center;
  padding: 16px;
  z-index: 0;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(#212121, 0.9);
    backdrop-filter: blur(10px);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  &__text {
    font-size: 24px;
    font-weight: 600;
  }

  &__btn {
    text-transform: inherit;
    color: var(--text-color);
    background: #fff;
    margin-top: 24px;

    &:hover {
      background: var(--main-color);
      color: #fff;
      .btn-icon {
        color: #fff;
      }
    }

    .btn-icon {
      color: var(--main-color);
    }
  }
}

.profile__media .banner-chat,
.profile__posts .banner-chat {
  display: none;
}

.profile__videos {
  position: relative;
  .show-videos {
    cursor: pointer;
    color: var(--main-color);
    line-height: 1;
    position: absolute;
    top: 16px;
    right: 16px;
    &:hover {
      text-decoration: underline;
    }
  }
}

/* ------------------- Media -------------------- */
@media (max-width: 1024px) {
  .profile__body {
    grid-template-columns: 320px auto;
  }

  .profile__looking {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  // .profile__options-btn {
  //   width: 48px;
  //   height: 48px;
  // }

  // .profile__options-label {
  //   display: none;
  // }
}

@media (max-width: 992px) {
  .profile__main-info,
  .profile__interview,
  .profile__media,
  .profile__posts {
    display: none;
    animation: var(--animation-fade);

    &.active {
      display: block;
    }
  }

  .profile__interview .left-block {
    padding: 0;
    border: none;
    margin-bottom: 0;
  }

  .profile__media .banner-chat,
  .profile__posts .banner-chat {
    display: flex;
  }

  .profile .container {
    width: 100%;
    max-width: 800px;
  }

  .profile__body {
    display: flex;
    flex-direction: column;
    gap: 0;
  }

  .profile__right {
    order: -1;
  }

  .profile__photo {
    display: none;
  }

  .profile__header {
    display: none;
  }

  .profile__populars-ladies {
    display: none;
  }

  .profile .profile-slider-inner {
    display: block;
  }

  .profile__photos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 16px;
    margin-bottom: 16px;
  }

  .profile__top-info {
    margin-bottom: 16px;
  }

  .profile__top-info .profile-id {
    display: none;
  }
}

@media (max-width: 640px) {
  .profile__photos {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .profile .mob-photo--vert .mob-photo__img {
    width: 100%;
  }

  .profile .mob-slide__text {
    font-size: 20px;
  }

  .profile .mob-slide__btn {
    width: 240px;
  }
}

@media (max-width: 390px) {
  .profile .left-block__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
