.live-chat {
  width: 440px;
  min-height: 400px;
  max-height: 80%;
  background: #fff url("../img/bg-chat.jpg") no-repeat center;
  box-shadow: 0px 0px 40px 0px rgba(73, 76, 86, 0.4);
  border-radius: 16px;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  bottom: -100%;
  right: 16px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 101;
  overflow: hidden;
  transition: all 0.5s ease-in-out;

  &.active {
    bottom: 16px;
    opacity: 1;
    visibility: visible;
    pointer-events: all;

    @media (max-width: 992px) {
      bottom: 80px;

      &.live-chat--pages {
        bottom: 16px;
        max-height: 85%;
      }
    }
  }

  &__date {
    width: 100%;
    text-align: center;
    margin-bottom: 16px;
  }

  &__close {
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    background-color: #ccc;
    border: 2px solid #fff;
    border-radius: 50%;
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 1;
    transition: background-color 0.3s ease-in-out;

    .material-icons {
      color: #fff;
    }

    &:hover {
      background-color: var(--pink-color);
    }
  }

  &__body {
    padding: 16px;
    padding-bottom: 0;
    width: 100%;
    height: 90%;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 4px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(#000, 0.15);
    }

    &::-webkit-scrollbar-thumb {
      background-color: rgba(#000, 0.2);
      border-radius: 100px;
    }
  }

  &__photo {
    width: 96px;
    height: 144px;
    cursor: pointer;
    display: block;
    float: right;
    border-radius: 8px;
    border: 2px solid #fff;
    margin-top: 16px;
    overflow: hidden;
    transition: all 0.3s ease-in-out;

    &.photo-hor {
      width: 220px;
    }

    &:hover {
      opacity: 0.8;
      transform: scale(1.02);
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.3s ease-in-out;
    }
  }

  &__footer {
    flex-shrink: 0;
    width: 100%;
    height: 64px;
    display: flex;
    gap: 16px;
    border-top: 1px solid var(--line-dark);
    background: #fff;
    position: relative;
  }

  &__form {
    height: 100%;

    .form-field {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      gap: 4px;
      position: relative;
    }

    .form-field__textarea {
      width: 100%;
      height: 100%;
      display: flex;
      resize: inherit;
      margin-bottom: 0;
      border: none;
      padding-left: 0;
      padding-right: 8px;
      padding-top: 12px;
      padding-bottom: 8px;
    }

    .chat-form__btn {
      flex-shrink: 0;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      align-self: center;
      position: relative;
      top: inherit;
      right: 8px;
    }

    .btn-chat__icon {
      width: 20px;
      height: 20px;
      font-size: 20px;
    }
  }

  &__options {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 8px;
    padding-left: 16px;
  }

  .add-photo {
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 22px;
    height: 22px;
    position: relative;
    overflow: hidden;

    .material-icons {
      display: inline-block;
      width: 22px;
      height: 22px;
      font-size: 22px;
    }

    &__input {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      display: none;
    }

    &__label {
      width: 100%;
      height: 100%;
      cursor: pointer;
      display: inline-block;
    }

    &__icon {
      color: var(--icon-gray);
      transition: color 0.3s ease-in-out;

      &:hover {
        color: var(--blue-color);
      }
    }
  }

  .chat-item {
    margin-bottom: 12px;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }
}

.btn-chatsmile {
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  height: 22px;
  background: url("../img/icons/ic-smile_n.svg") no-repeat center;
  background-size: cover;
  transition: background-image 0.3s ease-in-out;

  &:hover,
  &.active {
    background: url("../img/icons/ic-smile_h.svg") no-repeat center;
    background-size: cover;
  }
}

.fg-emoji-container {
  border-radius: 16px !important;
}

.fg-emoji-nav ul li.emoji-picker-nav-active a {
  background-color: var(--blue-color) !important;

  svg {
    fill: #fff !important;
  }
}

.fg-picker-special-buttons a {
  background-color: var(--pink-color) !important;
  transition: all 0.3s ease-in-out;

  &:hover {
    svg {
      fill: #fff !important;
    }
  }
}

@media (max-width: 1024px) {
  .live-chat {
    .btn-chatsmile {
      display: none;
    }
  }
}

@media (max-width: 480px) {
  .live-chat {
    width: 95%;
    right: inherit;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 0px 0 40px 10px rgba(73, 76, 86, 0.4);
  }
}
