.read-more {
  display: none;
  width: 24px;
  height: 24px;
  font-size: 24px;
  transition: transform 0.3s ease-in-out;

  &__icon {
    fill: var(--main-color);
    color: var(--main-color);
  }

  &.pink {
    color: var(--pink-color);
    .read-more__icon {
      fill: var(--pink-color);
      color: var(--pink-color);
    }
  }

  &.blue {
    color: var(--blue-color);
    .read-more__icon {
      fill: var(--blue-color);
      color: var(--blue-color);
    }
  }

  &.active {
    display: inline-block;
  }
}
