.modal-interview {
  .modal__header {
    margin-bottom: 24px;
  }

  &__search {
    margin-bottom: 16px;
  }
}

.popular-questions {
  &__title {
    font-size: 20px;
    margin-bottom: 12px;
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-bottom: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid var(--line-dark);
  }

  &__item {
    cursor: pointer;
    .radio__label {
      font-size: 16px;
      font-weight: 500;
      span {
        color: var(--title-color);
        font-weight: 600;
      }
    }
  }

  &__result {
    margin-bottom: 16px;

    &-title {
      font-size: 18px;
      font-weight: 700;
      margin-bottom: 8px;
    }
  }

  &__selected {
    width: 100%;
    min-height: 56px;
    padding: 16px;
    background: #fff;
    border: 1px solid var(--blue-color);
    border-radius: 8px;
  }
}

.modal-interview__search {
  .dropdown__list {
    max-height: 180px;
    overflow-y: auto;
  }

  .dropdown__item {
    padding: 10px 16px;
    &:hover {
      background: var(--gray-color_2);
    }

    .radio {
      cursor: pointer;
      width: 100%;
      height: 100%;
    }

    .radio__label {
      cursor: pointer;
    }

    &:hover .radio__label {
      width: 100%;
    }
  }
}

@media (max-width: 480px) {
  .modal-interview {
    .modal__header {
      margin-bottom: 16px;
    }

    .modal__content {
      margin: 0 auto;
      margin-top: 0;
      margin-bottom: auto;
    }
  }

  .modal-interview .modal__descr {
    display: none;
  }

  .popular-questions__title {
    font-size: 18px;
  }

  .popular-questions__list {
    margin-bottom: 16px;
    padding-bottom: 0;
    border: none;
  }

  .popular-questions__selected {
    min-height: 48px;
    padding: 8px 16px;
  }

  .popular-questions__result-title {
    font-size: 16px;
  }
}
