.header {
  padding: 12px 0;
  background: var(--dark-color);
  position: relative;
  z-index: 100;

  &-fixed {
    width: 100%;
    display: flex;
    align-items: center;
    background: rgba(#212121, 0.8);
    backdrop-filter: blur(10px);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    transition: all 0.3s ease-in-out;
    padding: 8px 0;

    .header__left {
      display: none;
    }

    .menu__link-name {
      display: none;
    }

    .header .menu__link {
      padding: 0 48px;
    }

    .menu__link::after {
      bottom: -23px !important;
    }

    .header__account-body {
      display: none;
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }

  &__left,
  &__right {
    display: flex;
    align-items: center;
  }

  &__chat {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
    height: 40px;
    border: 1px solid var(--line-light);
    border-radius: 6px;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    padding: 0 16px;
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-left: 16px;

    &:hover {
      border: 1px solid transparent;
      background: #fff;
      color: var(--text-color);
    }

    &-icon {
      flex-shrink: 0;
      display: inline-block;
      width: 20px;
      height: 20px;
      background: url("../img/icons/ic-chat_h.svg") no-repeat center;
      background-size: cover;
      margin-right: 8px;
    }
  }

  &__account {
    cursor: pointer;
    text-decoration: none;
    display: flex;
    align-items: center;
    margin-right: 16px;

    &:hover {
      .header__account-photo {
        border-color: var(--main-color);
        transform: scale(1.02);
      }
    }
  }

  &__account-photo {
    flex-shrink: 0;
    border: 2px solid rgba(#fff, 0.15);
    transition: all 0.3s ease-in-out;
  }

  &__account-body {
    margin-left: 12px;
  }

  &__account-name {
    color: #fff;
    font-weight: 700;
    line-height: 1;
  }

  &__account-data {
    color: rgba(255, 255, 255, 0.6);
    font-size: 14px;
    font-weight: 400;
  }

  .nav-overlay {
    display: none;
    width: 100%;
    height: 100vh;
    background: var(--overlay-modal);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    animation: var(--animation-fade);
    &.active {
      display: block;
    }
  }

  .menu {
    list-style: none;
    display: flex;
    align-items: center;

    &__link {
      cursor: pointer;
      text-decoration: none;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      gap: 4px;
      color: rgba(255, 255, 255, 0.5);
      font-size: 14px;
      font-weight: 600;
      padding: 0 24px;
      transition: all 0.3s ease-in-out;
      position: relative;

      .noti-indicator {
        top: -6px;
        right: -2px;
        background: var(--system-color);
      }

      &::after {
        content: "";
        width: 100%;
        height: 4px;
        background: var(--main-color);
        position: absolute;
        bottom: -12px;
        left: 0;
        transform: scale(0);
        transition: transform 0.3s ease-in-out;
      }

      &:hover {
        color: #fff;

        &::after {
          transform: scale(1);
        }

        .menu__icon {
          fill: var(--main-color);
        }
      }

      &.active {
        cursor: default;
        color: #fff;

        &::after {
          transform: scale(1);
        }

        .menu__icon {
          fill: var(--main-color);
        }
      }
    }

    &__icon {
      fill: rgba(255, 255, 255, 0.4);
      transition: fill 0.3s ease-in-out;
    }
  }

  &__burger {
    display: none;
    position: absolute;
    top: 8px;
    right: 8px;
  }

  &__signout {
    text-decoration: none;
    cursor: pointer;
    width: 32px;
    height: 32px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    color: rgba(255, 255, 255, 0.4);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--main-color);
    }
  }

  &__signout-mob {
    display: none;
  }
}

.logo {
  cursor: pointer;
  display: inline-flex;
  align-items: center;

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: inline-block;
  }
}

.header .signout-mob {
  display: none;
}

.header-mail.header-fixed {
  position: relative !important;
  top: inherit !important;
  left: inherit !important;
  right: inherit !important;
}

/* ------------------- Media -------------------- */
@media (max-width: 1200px) {
  .header__chat {
    display: none;
  }
}

@media (max-width: 1080px) {
  .header__chat {
    font-size: 0;
    gap: 0;
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 1024px) {
  .header {
    padding: 8px 0;
  }

  .header.header-fixed {
    .header__left {
      display: flex;
    }
  }

  .header__burger {
    display: inline-flex;
  }

  .header__left {
    gap: 16px;
  }

  .header__account {
    // display: none;
    margin-right: 40px;

    &-body {
      display: none;
    }

    &.account-mob {
      display: flex !important;
      padding: 12px;
      padding-right: 64px;

      .header__account-body {
        display: flex;
        flex-direction: column;
      }

      .header__account-name {
        color: var(--text-color);
      }

      .header__account-data {
        color: var(--text-color);
      }
    }
  }

  .header__nav {
    width: 320px;
    height: 100vh;
    overflow-y: auto;
    background: #fff;
    position: fixed;
    top: 0;
    right: -100%;
    transition: right 0.3s ease-in-out;
    z-index: 100;

    &.active {
      right: 0;
    }
  }

  .header .menu {
    flex-direction: column;
    align-items: flex-start;
  }

  .header .menu__item {
    width: 100%;
  }

  .header .menu__link {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 8px;
    color: var(--text-color);
    font-size: 16px;
    line-height: 1;
    padding: 16px;
    text-align: center;
    &:hover {
      color: var(--text-color);
    }

    &::after {
      display: none;
    }

    &.active {
      color: var(--text-color);
      background: var(--pink-light-color);
      border-left: 3px solid var(--main-color);
    }
  }

  .header .menu__link .noti-indicator {
    color: var(--system-color);
    font-weight: 800;
    background: none;
    border: 1px solid var(--system-color);
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
    &::before {
      content: "";
      display: inline-block;
      width: 8px;
      height: 8px;
      background: var(--system-color);
      border-radius: 50%;
      position: absolute;
      top: 5px;
      left: -16px;
      // transform: translateY(-50%);
      animation: shadowIndicator 1.5s infinite alternate;
    }
  }

  .header .menu__icon {
    width: 24px;
    height: 24px;
    fill: var(--icon-gray);
  }

  .header-fixed .menu__link-name {
    display: block;
  }

  .header__signout {
    display: none;
  }

  .header .signout-mob {
    display: flex;
  }

  .header__signout-mob {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 8px;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 16px;
    text-decoration: none;
    font-weight: 600;
    background: var(--gray-color_2);
    .material-icons {
      color: var(--icon-gray);
    }
  }
}

@media (max-width: 992px) {
  .header-profile {
    background: none;
    padding: 0;
    &::before {
      content: "";
      width: 100%;
      height: 72px;
      background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0.4) 100%
      );
      position: absolute;
      top: 0;
      left: 0;
    }

    .header__logo {
      position: absolute;
      top: 16px;
      left: 16px;
    }

    .header__account {
      display: none;
    }

    .header__burger {
      background: rgba(0, 0, 0, 0.6);
      backdrop-filter: blur(4px);
      position: fixed;
      top: 16px;
      right: 16px;
      z-index: 1000;
    }

    &.header-fixed {
      backdrop-filter: blur(0);
      position: fixed;
      &::before {
        display: none;
      }

      .header__logo {
        display: none;
      }
    }
  }
}
