.correspond-item {
  display: grid;
  grid-template-columns: 112px auto;
  gap: 16px;
  padding: 16px 0;
  position: relative;
  transition: all 0.3s ease-in-out;

  &:not(:last-child) {
    border-bottom: 1px solid var(--line-dark);
  }

  &:hover {
    border: 1.5px solid var(--blue-color);
    background: #fbfdff;
    border-radius: 16px;
    padding: 16px;

    .correspond-item__read {
      background: var(--blue-color);
      border-color: transparent;
      color: #fff;
      .material-icons {
        color: #fff;
      }
    }
  }

  &.selected {
    border: 1.5px solid var(--blue-color);
    background: #fbfdff;
    border-radius: 16px;
    padding: 16px;
    // margin-bottom: 16px;

    @media (max-width: 480px) {
      padding: 0;
      margin-bottom: 0;
      border-radius: 12px;
    }
  }

  &:first-child {
    &:after {
      display: none;
    }
  }

  .letter-item__info {
    font-weight: 600;
  }

  &__badge-new {
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    padding: 0 16px;
    padding-left: 10px;
    border-radius: 0 100px 100px 0;
    background-color: var(--pink-color);
    color: #fff;
    font-size: 13px;
    font-weight: 800;
    line-height: 1;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    position: absolute;
    bottom: 24px;
    left: 0;
    z-index: 1;
  }

  .profile__photo {
    flex-shrink: 0;
    display: block !important;
    width: 100%;
    height: 168px;
    border-radius: 8px;
    margin-bottom: 0;
    .profile-status {
      width: 16px;
      height: 16px;
      top: 8px;
      right: 8px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__right {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    // justify-content: space-between;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
  }

  &__check {
    width: 24px;
    height: 24px;
    overflow: hidden;
  }

  &__profile {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 8px;
  }

  &__profile-name {
    font-size: 18px;
    font-weight: 700;
  }

  .profile__location {
    font-size: 16px;
    &-icon {
      width: 18px;
      height: 18px;
      font-size: 18px;
    }
  }

  &__body {
    margin-bottom: 36px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }

  &__read {
    flex-shrink: 0;
    width: inherit;
    height: 40px;
    padding: 0 20px !important;
    padding-right: 12px !important;
    text-decoration: none;
    border-radius: 6px;
    border: 1.4px solid var(--line-dark);
    &:hover {
      color: #fff;
      background: var(--blue-color);
      .material-icons {
        color: #fff;
      }
    }
  }

  &__options {
    display: flex;
    align-items: center;
    .btn-favorite {
      margin-right: 12px;
    }
  }

  .btn-favorite {
    width: 40px;
    height: 40px;
    border-radius: 6px;
  }

  .btn-delete {
    border: 1.4px solid var(--line-dark);
    border-radius: 6px;
    width: 40px;
    height: 40px;
    padding: 0;
    .material-icons {
      color: var(--icon-gray);
    }

    &:hover {
      border-color: transparent;
      background: var(--system-color);
      .material-icons {
        color: #fff;
      }
    }
  }
}

// New
.correspond-item--new {
  padding: 16px;
  border: 2px solid var(--pink-color);
  border-radius: 16px;

  &:not(:last-child) {
    border-bottom: 2px solid var(--pink-color);
  }
  &.selected {
    margin-bottom: 0;
    border: 2px solid var(--pink-color);
  }
}

.correspond-item--archive {
  padding: 16px;
  border: 2px solid var(--system-color);
  border-radius: 16px;

  &:not(:last-child) {
    border-bottom: 2px solid var(--system-color);
  }
  &.selected {
    background: #fff9f8;
    border: 2px solid var(--system-color) !important;
    margin-bottom: 0;
  }
}

@media (max-width: 480px) {
  .correspond-item {
    padding: 0;
    overflow: hidden;
    border: 1px solid var(--line-dark);
    border-radius: 12px;

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &::after {
      display: none;
    }

    &__header {
      margin-bottom: 0;
    }

    .profile__location {
      flex-direction: column;
      align-items: flex-start;
      position: relative;
      padding-left: 20px;
      font-size: 15px;
      .profile__location-icon {
        position: absolute;
        top: 3px;
        left: 0;
      }
    }

    &__right {
      padding: 12px;
      padding-left: 0;
    }

    &__body {
      margin-bottom: 29px;
    }

    .profile__photo {
      border-radius: 0;
    }

    &:hover {
      border-radius: 12px;
      padding: 0;
    }

    &__profile {
      margin-bottom: 4px;
    }

    &__profile-name {
      font-size: 16px;
    }

    &__profile-id {
      display: none;
    }

    &__footer {
      align-items: flex-end;
    }

    &--new,
    &--archive {
      border-radius: 12px;
      border-width: 2px;
      padding-top: 0;
      padding-bottom: 0;
    }

    &--new {
      border-color: var(--pink-color);
    }
    &--archive {
      border-color: var(--system-color);
    }
  }

  .correspond-item .profile__location .city {
    display: none;
  }

  .correspond-item .letter-item__info {
    gap: 4px;

    .letter-item__time {
      position: relative;
      padding-left: 10px;
      &::before {
        display: inline-block;
        content: "/";
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  .correspond-item__options {
    .btn {
      width: 24px;
      height: 24px;
      border: transparent;
    }
    .btn-favorite {
      width: 24px;
      height: 24px;
      border: transparent;
      margin-right: 16px;
    }
  }

  .correspond-item__badge-new {
    bottom: 16px;
  }
}

@media (max-width: 390px) {
  .correspond-item__data {
    .material-icons {
      display: none !important;
    }
  }
}

@media (max-width: 360px) {
  .correspond-item {
    grid-template-columns: 100px auto;
    gap: 12px;
  }
  .correspond-item__read {
    padding: 0 16px !important;
    padding-right: 8px !important;
  }
}
