.modal-report {
  a {
    color: var(--link-color);
  }

  &__text {
    font-size: 18px;
    margin-bottom: 16px;
    text-align: center;
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 8px;
    margin-bottom: 24px;

    li {
      position: relative;
      padding-left: 32px;
      &::before {
        content: "";
        display: inline-block;
        width: 24px;
        height: 24px;
        background: url("data:image/svg+xml;base64,")
          no-repeat center;
        background-size: cover;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    &:not(:last-child) {
      padding-bottom: 16px;
      margin-bottom: 16px;
      border-bottom: 1px solid var(--line-dark);
    }
  }

  &__link {
    text-decoration: none;
    display: flex;
    align-items: center;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 8px;
    transition: opacity 0.2s ease-in-out;
    &:hover {
      opacity: 0.8;
    }
  }

  &__block-text {
    color: rgba(#000, 0.6);
    font-size: 14px;
    font-weight: 500;
  }
}

@media (max-width: 480px) {
  .modal-report__text {
    font-size: 16px;
  }

  .modal-report__link,
  .modal-report__list {
    font-size: 15px;
  }

  .modal-report__block:not(:last-child) {
    padding-bottom: 12px;
    margin-bottom: 10px;
  }
}
