.chat-item {
  display: flex;

  &:not(:last-child) {
    margin-bottom: 12px;
  }

  &__wrapper {
    // width: 90%;
    max-width: 90%;
    min-width: 140px;
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding: 12px;
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 4px;
  }

  &__date {
    color: var(--text-gray);
    font-size: 13px;
  }

  &__status {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url("../img/icons/ic-chat_unread.svg") no-repeat center;
    background-size: cover;

    &.read {
      background: url("../img/icons/ic-chat_read.svg") no-repeat center;
      background-size: cover;
    }
  }

  .read-more {
    font-size: 15px;
  }

  &__profile {
    font-size: 16px;
    font-weight: 700;
  }

  &__message {
    font-size: 15px;
  }
}

// ! Men & Woomen
.chat-item--man {
  .chat-item__wrapper {
    background: #eff7fc;
    border-radius: 0 16px 16px 16px;

    .read-more {
      color: var(--link-color);
    }

    .chat-item__profile {
      color: var(--blue-color);
    }
  }
}

.chat-item--woman {
  justify-content: flex-end;
  .chat-item__wrapper {
    background: #fdf2f5;
    border-radius: 16px 0 16px 16px;
    .read-more {
      color: var(--pink-color);
    }

    .chat-item__profile {
      color: var(--pink-color);
    }
  }
}
