/* ------------------- private-gallery -------------------- */
.private-gallery {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(32px);
  padding: 48px;

  &.active {
    display: flex;
  }

  &__icon {
    display: inline-block;
    width: 48px !important;
    height: 48px !important;
    font-size: 48px !important;
    color: #fff;
    opacity: 0.4;
    margin-bottom: 16px;
  }

  &__title {
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 8px;
  }

  &__text {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 32px;
    opacity: 0.8;
  }

  &__btn {
    width: 240px !important;
  }
}

@media (max-width: 480px) {
  .private-gallery__title {
    font-size: 18px;
  }

  .private-gallery__text {
    font-size: 16px;
  }
}
