.profile-report {
  cursor: pointer;
  display: inline-block;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/ic-report.svg");
  background-position: center;
  background-size: cover;
  border-radius: 4px;
  transition: background-image 0.3s ease-in-out;
  &:hover {
    background-image: url("../img/icons/ic-report_red.svg");
    // opacity: 0.8;
  }
}
