.correspond {
  &__options {
    display: flex;
    justify-content: flex-end;
    // justify-content: flex-end;
    align-items: center;

    .btn {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      padding: 0;
      width: 48px;
      height: 48px;
      background: transparent;
      border: 1.4px solid var(--line-dark);

      &:not(:last-child) {
        margin-right: 12px;
      }

      .material-icons {
        color: var(--icon-gray);
      }
    }
  }

  &__option {
    position: relative;

    .dropdown {
      min-width: 214px;
      left: -162px;
    }
  }

  .pagination {
    padding: 0;
  }
}

.correspond-sort {
  &__btn {
    &:hover,
    &.active {
      opacity: 1;
      background: var(--blue-color);
      border-color: transparent;
      .material-icons {
        color: #fff;
      }
    }

    .material-icons {
      width: 32px;
      height: 32px;
      font-size: 32px;
    }
  }
}

.correspond-delete {
  &__btn {
    &:hover,
    &.active {
      opacity: 1;
      background: var(--system-color);
      border-color: transparent;
      .material-icons {
        color: #fff;
      }
    }
  }
}

.correspond__block {
  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.correspond__list {
  display: flex;
  flex-direction: column;

  .correspond-item {
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &.total-corespondence {
    .correspond-item {
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }
}

@media (max-width: 480px) {
  .correspond__block-title {
    margin-bottom: 8px;
  }

  .correspond__options,
  .correspond__option {
    width: 100%;
    justify-content: flex-start;
  }

  .correspond__options {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
    margin-bottom: 16px;
  }

  .correspond__options .btn {
    width: 100%;

    &:not(:last-child) {
      margin-right: 0;
    }
  }

  .correspond__option .dropdown {
    left: 0;
  }

  .correspond__list.total-corespondence {
    .correspond-item {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
}
