.preview-letters {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
  position: relative;
  z-index: 10;

  &__link {
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    line-height: 1;
    padding: 8px 16px;
    background: #f8f8f8;
    border-radius: 100px;
    border: 1px solid var(--line-dark);
    transition: all 0.3s ease-in-out;
    &:hover {
      background: #fff;
      border-color: var(--blue-color);
    }
  }

  &__date {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    &-input {
      position: relative;
    }
    &-icon {
      cursor: pointer;
      display: inline-block;
      color: var(--icon-gray);
      transition: color 0.3s ease-in-out;
      position: relative;
      &:hover {
        color: var(--blue-color);
      }
    }
  }
}
