.modal-profile-contacts {
  &__block {
    display: none;
    &.show {
      display: block;
    }
  }

  .modal__header {
    padding-bottom: 24px;
    border-bottom: 1px solid var(--line-dark);
    margin-bottom: 24px;
  }

  .modal__descr {
    font-size: 18px;
    opacity: 1;
  }

  &__photo {
    display: inline-block;
    width: 104px;
    height: 104px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 8px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__body {
    p {
      margin-bottom: 24px;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 16px;
    li {
      display: flex;
      align-items: center;
      gap: 8px;
      .material-icons {
        color: var(--main-color);
        display: inline-block;
        width: 22px;
        height: 22px;
        font-size: 22px;
      }

      a {
        text-decoration: none;
        transition: color 0.2s ease-in-out;
        &:hover {
          color: var(--main-color);
          text-decoration: underline;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .modal-profile-contacts__photo {
    width: 80px;
    height: 80px;
  }

  .modal-profile-contacts .modal__header {
    padding-bottom: 16px;
    margin-bottom: 16px;
  }

  .modal-profile-contacts {
    .textbold-6 {
      font-size: 18px;
    }
  }
}
