.photos-more {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: #fff;
  background: rgba(#000, 0.5);
  border-radius: 8px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;

  &.blur {
    background: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(8px);
  }

  &__text {
    font-size: 14px;
    font-weight: 700;
    line-height: 1;
  }

  &__number {
    font-size: 28px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 4px;
  }
}
