.profile-specific {
  margin-top: 16px;

  &__header {
    display: flex;
    justify-content: left;
    align-items: center;
    margin-bottom: 16px;
    position: relative;
  }

  &__tabs {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__item {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 8px;
    font-weight: 400;
    transition: all 0.3s ease-in-out;

    &-icon {
      color: var(--icon-gray);
    }

    &:hover {
      color: var(--main-color);

      .profile-specific__item-icon {
        color: var(--main-color);
      }
    }

    &.active {
      font-weight: 700;
      cursor: default;
      .profile-specific__item-icon {
        color: var(--main-color);
      }

      &:hover {
        color: var(--text-color);
      }
    }
  }

  &__settings {
    cursor: pointer;
    display: inline-block;
    width: 24px;
    height: 24px;
    font-size: 24px;
    color: var(--icon-gray);
    // position: relative;
    // left: 8px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    transition: color 0.2s ease-in-out;
    &:hover {
      color: var(--main-color);
    }
  }
}
