.dropdown {
  width: 100%;
  background: var(--white-color);
  box-shadow: var(--bx-dropdown);
  border-radius: var(--br-8);
  padding: 8px 0;
  position: absolute;
  top: calc(100% + 4px);
  left: 0;
  z-index: 10;

  display: none;
  animation: var(--animation-fade);

  &.active {
    display: block;
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;
  }

  &__item {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 8px 16px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
    padding: 12px 15px;
    transition: background-color 0.25s ease-in-out;
    position: relative;

    &.active {
      color: #fff;
      opacity: 1;
      background: var(--blue-color);
      font-weight: 600;

      &:hover {
        color: #fff;
        opacity: 1;
        background: var(--blue-color);
        font-weight: 600;
      }

      &::after {
        content: "";
        position: absolute;
        top: 53%;
        -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        right: 16px;
        width: 20px;
        height: 20px;
        background: url("data:image/svg+xml;base64,");
        background-size: cover;
        background-repeat: no-repeat;
        transition: all 0.4s ease-in-out;
        opacity: 0.8;
      }
    }

    &:hover {
      background-color: var(--blue-light-color);
      opacity: 1;

      .search-result__link,
      .search-result__link-icon {
        color: #fff;
      }
    }
  }
}
