.profile-item {
  cursor: pointer;
  display: block;
  height: 260px;
  // aspect-ratio: 2 / 3;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  text-decoration: none;
  z-index: 0;
  border: 3px solid transparent;
  transition: all 0.3s ease-in-out;

  &:hover {
    .profile-item__hover {
      opacity: 1;
    }
  }

  &__hover {
    width: 100%;
    height: 100%;
    background: rgba(#333, 0.8);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;

    &:hover {
      transition: opacity 0.3s ease-in-out;
      .profile-item__options {
        left: 12px;
      }
    }
  }

  &__options {
    display: flex;
    flex-direction: column;
    gap: 12px;
    position: absolute;
    top: 12px;
    left: -100%;
    transition: left 0.3s ease-in-out;

    .option {
      cursor: pointer;
      text-decoration: none;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      color: var(--pink-color);
      background: #fff;
      border-radius: 50%;
      transition: all 0.3s ease-in-out;
      span {
        display: inline-block;
        width: 20px;
        height: 20px;
        font-size: 20px;
      }

      &:hover {
        color: #fff;
        background: var(--pink-color);
      }
    }
  }

  &::before {
    display: inline-block;
    content: "";
    width: 100%;
    height: 100%;
    background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      color-stop(41.24%, rgba(0, 0, 0, 0)),
      color-stop(99.66%, rgba(0, 0, 0, 0.8))
    );
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 41.24%,
      rgba(0, 0, 0, 0.8) 99.66%
    );
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
  }

  .profile-status {
    position: absolute;
    top: 12px;
    right: 12px;
    z-index: 0;
  }

  &__photo {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;

    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      transition: transform 0.3s ease-in-out;
    }
  }

  &:hover {
    transform: scale(1.02);
    @media (max-width: 1024px) {
      transform: none;
    }

    .profile-item__photo img {
      transform: scale(1.1);
      @media (max-width: 1024px) {
        transform: none;
      }
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    padding: 12px;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 0;
  }

  &__loc {
    color: #fff;
    display: flex;
    align-items: center;
    gap: 4px;

    .material-icons {
      display: inline-block;
      width: 20px;
      height: 20px;
      font-size: 20px;
      color: rgba(#fff, 0.5);
    }
  }

  &__name {
    color: #fff;
    line-height: 1.1;
    font-weight: 700;
  }

  &__status {
    max-width: max-content;
    text-align: center;
    border-radius: 100px;
    border-radius: 100px;
    padding: 4px 12px;
    color: #fff;
    font-size: 12px;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 8px;
    &.new {
      background: var(--pink-color);
    }
    &.block {
      background: var(--system-color);
    }
  }

  &__favorite {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 12px;
    right: 12px;
    .yes-favorite {
      display: inline-block;
      width: 20px;
      height: 20px;
      font-size: 20px;
      color: var(--pink-color);
    }
    .no-favorite {
      display: inline-block;
      width: 20px;
      height: 20px;
      font-size: 20px;
      color: rgba(#fff, 0.5);
    }
  }
}
